import {LOGGING} from '@devsnc/library-opws-mapping-shared';
import '@servicenow/now-alert';
import {priority} from './notifications/priority';
import {groupOfGroups} from './notifications/groupOfGroups';

const NOTIFICATION_RENDERERS = {
	PRIORITY: priority,
	GROUP_OF_GROUPS: groupOfGroups
};

const handler = {
	get: (target, prop) => prop
};
export const NOTIFICATION = new Proxy(NOTIFICATION_RENDERERS, handler);

export const snEmAiOverviewNotificationView = state => {
	const {properties: {show, type}} = state;
	LOGGING.info('snEmAiOverviewNotificationView', {
		caller: 'snEmAiOverviewNotificationView',
		extra: {show, type}
	});

	if (!show)
		return null;

	const fn = NOTIFICATION_RENDERERS[type];
	return fn(state);
};
