import {LOGGING} from '@devsnc/library-opws-mapping-shared';
import {t} from 'sn-translate';
import '@servicenow/now-empty-state';
import './components/snEmAiOverviewChartBarWrapper';

const headerText = t('No properties found');
const bodyText = t('Currently there is no data to display');

export const view = ({properties}) => {
	const {contentData} = properties;
	LOGGING.info('snEmAiOverviewMeaningfulLogPropertiesView', {
		force: true,
		caller: 'snEmAiOverviewMeaningfulLogPropertiesView',
		extra: {contentData}
	});

	return (contentData !== null && contentData.length > 0) ?
		(<div className='content'>
			{contentData.map(
				entity => <sn-em-ai-overview-chart-bar-wrapper entity={entity}></sn-em-ai-overview-chart-bar-wrapper>)}
		</div>) : (
			<now-empty-state
				variant='auto'
				type='empty'
				body-text={bodyText}
				header-text={headerText}/>
		);
};
