import {createCustomElement} from '@servicenow/ui-core';
import {t} from 'sn-translate';
import {meaningfulEntitiesEffect} from './actions/meaningfulEntitiesEffect';
import {SYS_ID} from '../../constants/uiTypes';

/**
 * This is a provider component responsible for fetching the meaningful entities mapping.
 *
 * ```jsx
 * <sn-em-ai-overview-meaningful-entities  sysId=''  />
 * ```
 *
 * @seismicElement 'sn-em-ai-overview-meaningful-entities'
 * @summary fetching the mapping of meaningful entities for that alert.
 */
createCustomElement('sn-em-ai-overview-meaningful-entities', {
	properties: {
		/**
         * Specifies a specific Alert's sys_id.
         * @type {string}
         */
		sysId: {
			default: undefined,
			required: true,
			schema: SYS_ID
		},
		tooltip: {
			default: {
				trigger: t('What are meaningful log properties?'),
				content: t('Shows the distribution of properties extracted from the log automatically or manually by a user')
			},
			selectable: true
		},
		contentData: {
			default: [],
			selectable: true
		},
	},
	actions: {
		...meaningfulEntitiesEffect.actions,
	},
	actionHandlers: {
		...meaningfulEntitiesEffect.actionHandlers,
	}
});
