import {CONSTANTS, checkForError, LOGGING} from '@devsnc/library-opws-mapping-shared';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {actionTypes} from '@servicenow/ui-core';

const {COMPONENT_BOOTSTRAPPED} = actionTypes;

const ACTION_TYPES = {
	CONFIGURATION_ITEM_REQUESTED: 'CONFIGURATION_ITEM#REQUESTED',
	CONFIGURATION_ITEM_STARTED: 'CONFIGURATION_ITEM#STARTED',
	CONFIGURATION_ITEM_SUCCEEDED: 'CONFIGURATION_ITEM#SUCCEEDED',
	CONFIGURATION_ITEM_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED
};

const GQL_QUERY = `
query getCMDBRecord($queryConditions: String!) {
  GlideRecord_Query {
    cmdb_ci(queryConditions: $queryConditions) {
      _results {
        sys_id {
          value
        },
        name {
          displayValue
        },
        sys_class_name {
          displayValue
        },
        location {
          displayValue
        },
        assigned_to {
       	   displayValue,
          _reference {
            avatar {
              displayValue
            }
          }
        },
        owned_by {
            displayValue
        }
      }
    }
  }
}
`;

const getCmdbRecordMetaData = createGraphQLEffect(GQL_QUERY, {
	startActionType: ACTION_TYPES.CONFIGURATION_ITEM_STARTED,
	successActionType: ACTION_TYPES.CONFIGURATION_ITEM_SUCCEEDED,
	errorActionType: ACTION_TYPES.CONFIGURATION_ITEM_FAILED,
	variableList: ['queryConditions']
});


/**
 *
 * @param ciSysId - The sys_id of the Configuration Item associated with the alert
 * @returns {object}
 */
const getVariables = (ciSysId) => {
	return {
		queryConditions: `sys_id=${ciSysId}`
	};
};

const transform = (payload) => {
	const {data: {GlideRecord_Query: {cmdb_ci: {_results}}}} = payload;
	const {sys_id, name, sys_class_name, location, assigned_to, owned_by} = _results[0];

	const data = {
		sys_id: sys_id['value'],
		name: name['displayValue'],
		sys_class_name: sys_class_name['displayValue'],
		location: location['displayValue'],
		assigned_to: assigned_to['displayValue'] || '',
		assigned_to_avatar: assigned_to['_reference']['avatar']['displayValue'] || '',
		owned_by: owned_by['displayValue'] || ''
	};
	return data;
};

/**
 * Perform all the tasks needed to do after the remediation started to invoke
 * @param coeffects
 */
const setCmdbRecordMetaData = coeffects => {
	const {
		dispatch,
		updateState,
		updateProperties,
		action: {payload}
	} = coeffects;
	const errors = checkForError({payload, dispatch, caller: 'setCmdbRecordMetaData'});
	if (errors) {
		LOGGING.error('setCmdbRecordMetaData failed', errors);
		return;
	}

	const cmdbMetaData = transform(payload);
	updateState({cmdbMetaData});
	updateProperties({nowMode: 'active'});
};


const init = ({properties, updateProperties, dispatch}) => {
	const {sysId} = properties;
	LOGGING.info('snEmAiOverviewCiView-> init');

	if (!sysId) {
		LOGGING.info('snEmAiOverviewCiView-> In order to proceed  there must be ciSysId argument associated with the alert', {force: true, extra: {}});
		updateProperties({nowMode: 'active'});
		return;
	}
	dispatch(ACTION_TYPES.CONFIGURATION_ITEM_REQUESTED, getVariables(sysId), {stopPropagation: true});
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: init,
	[ACTION_TYPES.CONFIGURATION_ITEM_REQUESTED]: getCmdbRecordMetaData,
	[ACTION_TYPES.CONFIGURATION_ITEM_SUCCEEDED]: setCmdbRecordMetaData
};


const actions = {
	[ACTION_TYPES.CONFIGURATION_ITEM_REQUESTED]: {
		schema: {
			type: 'object',
			properties: {
				queryConditions: {type: 'string'}
			},
			required: ['queryConditions']
		}
	}
};


/**
 * Effect that handles both remediation / launch-application actions and dispatches actions accordingly:
 *  - remediation: invoke the flow designer sub flow with the specific job
 *  - launch-application: Open a new window with the specific custom URL
 *
 * @function snEmAiOverviewMetric
 * @example
 * import {snEmAiOverviewMetric} from '@sn-em-ai-overview-metric/snEmAiOverviewMetric';
 *
 * // Register with component action handlers:
 * {
 *     actionHandlers: {
 *			...snEmAiOverviewMetric.actionHandlers
 *     }
 * }
 *
 * @return {Object} snEmAiOverviewMetric Descriptor
 */
export const cmdbEffect = {
	ACTION_TYPES,
	getVariables,
	actionHandlers,
	actions
};
