import '@servicenow/now-card';
import '@servicenow/now-badge';
import '@servicenow/now-heading';
import '@servicenow/now-record-list-connected';
import Fragment from '@servicenow/ui-renderer-snabbdom/lib/fragment';
import {LOGGING} from '@devsnc/library-opws-mapping-shared';

const _constructQuery = (query, sortField = null, sortDesc = false) => {
	return query.concat(sortField ? '^ORDERBY' + (sortDesc ? 'DESC' : '') + sortField : '');
};

export const snEmAiOverviewListContentView = (state) => {
	const {
		properties: {
			rowCount: limit,
			maxCharLimit,
			wordWrap,
			table,
			query,
			orderByField,
			orderByDesc,
			columns
		}
	} = state;

	if (query == null) {
		LOGGING.info('Query is null, still loading...');
		return;
	}

	const additionalProps = {};
	if (limit !== null) additionalProps.limit = limit;
	if (maxCharLimit !== null) additionalProps.maxCharLimit = maxCharLimit;
	if (wordWrap !== null) additionalProps.wordWrap = wordWrap;

	const _query = _constructQuery(query, orderByField, orderByDesc);

	LOGGING.debug(`TABLE: ${table} QUERY: ${_query}`);

	//TODO: Replace listType='RELATED' with official exposed ACTION
	//(TMP Fix for DEF0136197) approved by Yotam
	return (<Fragment>
		<now-record-list-connected-snapshot
			hideCheckboxHover={true}
			hideColumnGrouping={true}
			hideDBViews={true}
			hideDeclarativeActions={true}
			hideHeader={true}
			hideHighlightedValues={false}
			hideInlineEditing={true}
			hideLastRefreshedText={true}
			hideLinks={false}
			hideLiveList={true}
			hideMenuButton={true}
			hideMultiEdit={true}
			hidePanel={true}
			hideQuickEdit={true}
			hideRefreshButton={true}
			hideRowSelector={true}
			hideSelectAll={true}
			hideTitle={true}
			hideTitleRowCount={true}
			hideUnnecessaryRowSelectors={true}
			hideViewAll={true}
			hidePagination={true}
			hideCellFilter={true}
			hideFilterPanel={true}
			hideColumnSorting={true}
			table={table}
			query={_query}
			columns={columns}
			listType='RELATED'
			{...additionalProps}
		/>
		<now-card-divider
			block-spacing={'none'}/>
	</Fragment>);
};