import Fragment from '@servicenow/ui-renderer-snabbdom/lib/fragment';
import {LOGGING} from '@devsnc/library-opws-mapping-shared';
import {NOTIFICATION} from './components/sn-em-ai-overview-notification/snEmAiOverviewNotificationView';
import {t} from 'sn-translate';
import '@servicenow/now-empty-state';
import {componentsFactory} from './snEmAiOverviewFactory';
import {ALERT_TYPE_KEY} from './constants/alertTypes';

const OVERVIEW_TAB = t('Overview tab');

const _componentInLayout = (leftColumn, rightColumn) => {
    return <Fragment>
		<span className='ai-left-column'>
			{leftColumn}
		</span>
        <span className='ai-right-column'>
			{rightColumn}
		</span>
    </Fragment>;
};

const _setRelevantComponent = (alertMetaData, pImpactedServices) => {
    const {
        sys_id: sysId,
        cmdb_ci: cmdbCi,
        layout
    } = alertMetaData;

    const {
        notification, rootCause, cisInGroup, impactedServices, alertListInGroup, topIncidents,
        topAlerts, identifiedIssue, anomaly, meaningfulEntities
    } = componentsFactory.getComponents(sysId);

    const renderedComponents = [];

    /**
     * each case represent a choice from the choices table:
     * the choices are:
     * automated, rulesBased, manual, CMDB, secondary, none, text, log analyticst incident, log analytics alert
     */
    switch (layout) {
        case ALERT_TYPE_KEY.STAND_ALONE_ALERT:
            /*6- none*/
            renderedComponents.push(notification(NOTIFICATION.PRIORITY, alertMetaData, pImpactedServices));
            renderedComponents.push(_componentInLayout([cisInGroup(sysId, cmdbCi), rootCause(sysId)],
                [impactedServices(sysId, pImpactedServices), topAlerts(sysId), topIncidents(sysId)]));
            break;
        case ALERT_TYPE_KEY.STAND_ALONE_LOG_ANALYTICS:
            renderedComponents.push(notification(NOTIFICATION.PRIORITY, alertMetaData, pImpactedServices));
            renderedComponents.push(_componentInLayout([identifiedIssue(sysId),cisInGroup(sysId, cmdbCi),
                    impactedServices(sysId, pImpactedServices),topAlerts(sysId), topIncidents(sysId)],
                [anomaly(sysId), meaningfulEntities(sysId)]));
            break;
        case ALERT_TYPE_KEY.GROUP_AUTOMATED:
            renderedComponents.push(notification(NOTIFICATION.PRIORITY, alertMetaData, pImpactedServices));
            renderedComponents.push(_componentInLayout([alertListInGroup(sysId),rootCause(sysId)],
                [cisInGroup(sysId, cmdbCi), impactedServices(sysId, pImpactedServices)]));
            break;
        case ALERT_TYPE_KEY.GROUP_MEMBER_STAND_ALONE:
            renderedComponents.push(notification(NOTIFICATION.PRIORITY, alertMetaData, pImpactedServices));
            renderedComponents.push(_componentInLayout([cisInGroup(sysId, cmdbCi), rootCause(sysId)],
                [impactedServices(sysId, pImpactedServices), topAlerts(sysId), topIncidents(sysId)]));
            break;
        case ALERT_TYPE_KEY.GROUP_MEMBER_LOG_ANALYTICS_STAND_ALONE:
            /*5- secondary */
            renderedComponents.push(notification(NOTIFICATION.PRIORITY, alertMetaData, pImpactedServices));
            renderedComponents.push(
                _componentInLayout(
                    [identifiedIssue(sysId), cisInGroup(sysId, cmdbCi), impactedServices(sysId, pImpactedServices),
                        topAlerts(sysId), topIncidents(sysId)], [anomaly(sysId), meaningfulEntities(sysId)]));
            break;
        case ALERT_TYPE_KEY.GROUP_MEMBER_COMPONENT_BASED:
            renderedComponents.push(
                _componentInLayout(
                    [identifiedIssue(sysId), cisInGroup(sysId, cmdbCi), impactedServices(sysId, pImpactedServices),
                        topAlerts(sysId), topIncidents(sysId)], [anomaly(sysId), meaningfulEntities(sysId)]));
            break;
        case ALERT_TYPE_KEY.GROUP_RULE_BASED:
            /* 1- automated 4- CMDB,  1-automated generalized [group.source =7 (ref to em_agg_group table)]*/
            renderedComponents.push(notification(NOTIFICATION.PRIORITY, alertMetaData, pImpactedServices));
            renderedComponents.push(_componentInLayout([alertListInGroup(sysId), rootCause(sysId)],
                [cisInGroup(sysId, cmdbCi), impactedServices(sysId, pImpactedServices), topIncidents(sysId), topAlerts(sysId)]));
            break;
        case ALERT_TYPE_KEY.GROUP_LOG_ANALYTICS:
            /*10- loom alert (Log Analytics Alert) (or parent.group_source = '10') */
            renderedComponents.push(notification(NOTIFICATION.GROUP_OF_GROUPS, alertMetaData, pImpactedServices));
            renderedComponents.push(_componentInLayout(alertListInGroup(sysId), [cisInGroup(sysId,cmdbCi), impactedServices(sysId,pImpactedServices)]));
            break;
        case ALERT_TYPE_KEY.GROUP_TEXT_BASED:
            LOGGING.error(`Alert type ${layout} doesn't have overview cards!`);
            break;
        case ALERT_TYPE_KEY.GROUP_COMPONENT_BASED:
            renderedComponents.push(_componentInLayout([identifiedIssue(sysId), cisInGroup(sysId, cmdbCi), impactedServices(sysId, pImpactedServices),
                topAlerts(sysId), topIncidents(sysId)], [anomaly(sysId), meaningfulEntities(sysId)]));
            break;
        default:
            LOGGING.error('snEmAiOverview->_setRelevantComponent failed selectedViewLayout-' + layout);
            return null;
    }
    const renderedProviders = Object.values(componentsFactory.getActiveProviders(sysId));
    const registeredComponents = componentsFactory.getActiveComponents(sysId);
    LOGGING.info('snEmAiOverview ->_setRelevantComponent', {
        force: true,
        caller: 'snEmAiOverview->_setRelevantComponent',
        extra: {registeredComponents, renderedProviders, renderedComponents}
    });
    return [...renderedComponents, ...renderedProviders];
};

/**
 /**
 * UI component that returns overview components and place them according to choices list :
 * there are 9 overview variations that returning component according to the data in the server:
 * automated, rulesBased, manual, CMDB, secondary, none, automated, task
 * the overview components are:
 * notifications, ci, impacted services, metric graph, probable root cause, top alerts, top incidents, ci's in group,
 * top group alerts.
 * @param state
 * @returns {HTMLObjects}
 */
export const snEmAiOverview = (state) => {
    const {alertMetaData, impactedServices} = state;

    if (!alertMetaData) {
        return <now-empty-state type='loading' variant='fit' widget={OVERVIEW_TAB}/>;
    }

    const {layout} = alertMetaData;
    LOGGING.info(`snEmAiOverview alert layout [${layout}]`);

    return (
        // FIXME need to update all layout CSS files
        <section className={`sn-overview layout-${layout}`}>
            {_setRelevantComponent(alertMetaData, impactedServices)}
        </section>
    );
};
