import {createCustomElement} from '@servicenow/ui-core';
import {snEmAiOverviewCiView} from './snEmAiOverviewCiView';
import styles from './snEmAiOverviewCi.scss';
import {cmdbEffect} from './actions/cmdbEffect';
import {LOGGING} from '@devsnc/library-opws-mapping-shared';
import {SYS_ID} from '../../constants/uiTypes';

/**
 * This component displays a box of a specific configuration item with its metadata:
 *  Class, Owner, Assigment Group
 *
 * ```jsx
 * <sn-em-ai-overview-ci sys-id=''></ssn-em-ai-overview-ci>
 * ```
 *
 * @seismicElement sn-em-ai-overview-ci
 * @summary The component displays a box of configuration item.
 */
createCustomElement('sn-em-ai-overview-ci', {
	view: snEmAiOverviewCiView,
	styles,
	initialState: {
		cmdbMetaData: null
	},
	properties: {
		/**
         * Specifies the CI sys_id associated to the alert if exists.
         * @type {string}
         */
		sysId: {
			default: undefined,
			schema: SYS_ID
		}
	},
	actionHandlers: {
		...cmdbEffect.actionHandlers
	},
	eventHandlers: [{
		events: ['click'],
		effect({properties}) {
			const {sysId} = properties;
			LOGGING.info('sn-em-ai-overview-ci->eventHandlers', {
				caller: 'sn-em-ai-overview-ci->eventHandlers',
				extra: {sysId}
			});

			if (!sysId)
				return;
		}
	}],
	/* dispatches is only for the use of the JS documentation */
	dispatches: {
		/**
         * Dispatched when the Notification component is loaded on the first time.
         * The payload is empty
         * @type {{value: object}}
         */
		'CONFIGURATION_ITEM#REQUESTED': {}
	}
});

