import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-score';
import '@servicenow/now-label-value';
import '@servicenow/now-chart-timeseries';
import '@servicenow/now-empty-state';
import {LOGGING} from '@devsnc/library-opws-mapping-shared';
import momentTz from 'moment-timezone';

const METRIC_TYPE = Object.freeze({
	RAW: 'RAW',
	AVERAGE: 'AVERAGE',
	EXPECTED: 'EXPECTED'
});

const LOCAL_TIMEZONE = 'LOCAL_TIMEZONE';
const ANOMALY_BASELINE_REFERENCE_INCREASE = 'ANOMALY_BASELINE_REFERENCE_INCREASE';
const ANOMALY_BASELINE_REFERENCE_DECREASE = 'ANOMALY_BASELINE_REFERENCE_DECREASE';
const DATAVIS_COLOR_PINK_5 = '#d954d9'; // its the same constant used for dataVis

const createScoreValue = score => {
	if (!score)
		return null;

	return <now-score-basic
		score={score.value}
		score-size={'sm'}
		heading={score.type}
		heading-position={'bottom'}
		hide-whitespace={true}/>;
};

const createDescription = descriptor => {
	if (!descriptor)
		return null;

	const items = [{
		label: descriptor.type,
		value: {
			type: 'string',
			value: descriptor.value
		}
	}];

	return <now-label-value-tabbed
		className='descriptor'
		items={items}
		delimiter=':'
		size='md'/>;
};

const getConfigurationBase = () => {
	return {
		width: 'auto',
		height: 'auto',

		alignTicks: true,
		showTooltip: true,
		hideRegister: true,

		yAxisType: 'linear',

		xAxisConfig: {
			tickConfig: {
				ticks: 3
			}
		},

		yFormat: (d) => `${d * 100 % 100 !== 0 ? d : parseInt(d)}`,

		yAxisConfig: {
			strokeWidth: 0,
			tickConfig: {
				disabled: true
			}
		},

		hideXGridlines: true,
		hideYGridlines: true
	};
};

const setConfig = (name, type, label, rest) => {
	return {
		[name]: {
			type,
			label,
			x: 'x',
			y: 'y',
			...rest
		}
	};
};
/**
 *  Update extents according to the given series properties
 * @param seriesProp
 * @return {{x: string[], y: string[]}|{x: string[], y: number[]}}
 */
const updateExtents = (seriesProp) => {
    const {AreAllValuesZero} = seriesProp;

    //When all the values are zero, we should set static upper bound
    const upperBound = AreAllValuesZero ? 10 : 'dynamic';

    return {
        x: ['dynamic', 'dynamic'],
        y: ['dynamic', upperBound]
    };
};
/**
 *  Update series properties so we can use that to set the extents
 * @param seriesProp
 * @param val
 */
const updateSeriesProp = (seriesProp, val) => {
    // Verify if all metrics values are zero
    if(seriesProp.AreAllValuesZero && val !== 0)
        seriesProp.AreAllValuesZero = false;
};

const fixData = (config, data, type, seriesProp, rest = {}) => {
	const {fixNumbers = false, updateDataType = false} = rest;

	const parseValues = {
		x: config[type].x,
		y: config[type].y
	};

	const fixedData = [];

	data.forEach(rec => {
		const newRec = {...rec};

		if (updateDataType)
			newRec.type = type;

		// set x/y values as numeric
		if (fixNumbers) {
			Object.keys(parseValues).forEach(key => {
				if (rec.hasOwnProperty(parseValues[key])) {
					const oldValue = rec[parseValues[key]];
					if (typeof oldValue === 'string') {
						newRec[parseValues[key]] = oldValue !== 'null' ? parseInt(oldValue, 10) : null;
					}
				}
			});
		}

		updateSeriesProp(seriesProp, newRec.y);

		fixedData.push(newRec);
	});

	// sort from oldest to newest timestamp
	fixedData.sort((a, b) => (a.x > b.x) ? 1 : -1);

	return fixedData;
};

/**
 *
 * @param count
 * @param expected
 * @param metrics
 * @param timezone
 * @param detectionType
 * @returns {*}
 */
const constructConfig = (count, expected, metrics, timezone, detectionType) => {
    const format = "HH:mm";
	const config = {...getConfigurationBase()};
    const seriesProp = {AreAllValuesZero: true};

	metrics.forEach(series => {
		switch (series.type) {
		case METRIC_TYPE.RAW:
			config.seriesConfig = setConfig(series.type, 'line', `${count.type}`, {showGaps: true});
			config.seriesData = fixData(config.seriesConfig, series.data, series.type, seriesProp);
			break;

		case METRIC_TYPE.EXPECTED:
			const color = detectionType !== undefined && detectionType === ANOMALY_BASELINE_REFERENCE_INCREASE ||
				detectionType === ANOMALY_BASELINE_REFERENCE_DECREASE ? {color: DATAVIS_COLOR_PINK_5} : {};

			config.confidenceBandConfig = setConfig(series.type, 'area', `${expected.tooltipType}`, color);
			config.confidenceBandData = fixData(config.confidenceBandConfig, series.data, series.type, seriesProp);
			break;

		case METRIC_TYPE.AVERAGE:
			config.thresholdConfig = setConfig(series.type, 'line', 'Average', {hideValue: true});
			config.thresholdData = fixData(config.thresholdConfig, series.data, series.type, seriesProp, {updateDataType: true});
			break;
		}
	});

    config.xFormat = (ts) => {
        if (timezone === LOCAL_TIMEZONE) {
            return momentTz.utc(ts).format(format);
        } else {
            return momentTz(ts).tz(timezone).format(format);
        }
    };

    config.extents = updateExtents(seriesProp);

	return config;
};

/**
 *
 * @param count
 * @param expected
 * @param metrics
 * @param timezone
 * @param detectionType
 * @returns {JSX.Element|null}
 */
const createChart = (count, expected, metrics, timezone, detectionType) => {
	if (!metrics)
		return null;

	const config = constructConfig(count, expected, metrics, timezone, detectionType);
	LOGGING.info('now-chart-timeseries config', {extra: config, output: {objects: true}});

	return <now-chart-timeseries className='anomaly-chart' {...config}/>;
};

/**
 * Return true if metrics is an empty array
 *
 * @param metrics
 * @returns {boolean}
 */
const isSignalDead = metrics => {
	return (Array.isArray(metrics) && !metrics.length);
};

/**
 * Return true if metrics array contains an object with type='RAW' and data=[]
 *
 * @param metrics
 * @returns {boolean}
 */
const isNewSignal = metrics => {
	if (Array.isArray(metrics)) {
		const raw = metrics.find(metric => metric.type === METRIC_TYPE.RAW);
		return Boolean(raw && raw.data && Array.isArray(raw.data) && !raw.data.length);
	}

	return false;
};

const createEmptyState = (descriptor) => {
	return <now-empty-state
		className='empty-state'
		type='empty'
		height='200' //TODO whole card should be 240px (rem?)
		variant='fit'
		headerText={descriptor.type}
		bodyText={descriptor.value}/>;
};

export const view = (state) => {
	const {metrics, count, expected, timezone, detectionType} = state.properties;

	if (isSignalDead(metrics) || isNewSignal(metrics))
		return createEmptyState(expected);

	const scoreValue = createScoreValue(count);
	const description = createDescription(expected);
	const chart = createChart(count, expected, metrics, timezone, detectionType);

	return <Fragment>
		{scoreValue}
		{description}
		{chart}
	</Fragment>;
}
;