import {LOGGING} from '@devsnc/library-opws-mapping-shared';
import cuid from 'cuid';
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-card';
import '@servicenow/now-popover';
import '@servicenow/now-button';
import './snEmAiOverviewPopoverWrapper';

const createContentWrapper = (content, triggerId) => <popover-content-wrapper
	slot='content'
	cssClass='popover-content-wrapper-override'
	triggerId={triggerId}
	label={content}/>;

const _createTooltipTail = (trigger, content) => {
	const triggerId = cuid();
	return 	<Fragment>
				<now-popover hide-tail='true'>
					<now-button-iconic
					id={triggerId}
					bare={true}
					variant='secondary'
					slot='trigger'
					size='sm'
					icon='circle-exclamation-outline'/>
					{createContentWrapper(content, triggerId)}
				</now-popover>
				<span className='ai-popover-trigger-title'>{trigger}</span>
			</Fragment>
};

const _createTooltipHead = (trigger, content) => {
	const triggerId = cuid();
	return <now-popover hide-tail='true'
		positions={['bottom-end', 'top-end']}>
		<now-button-iconic
			bare
			id={triggerId}
			slot='trigger'
			size='sm'
			icon='circle-exclamation-outline'
			tooltip-content={trigger}/>
		{createContentWrapper(content, triggerId)}
	</now-popover>;
};
/**
 *
 * @param label
 * @param link
 * @returns {*}
 * @private
 */
const _createFooterMetadata = (label) => {
	return <footer>
		<now-button-bare
			componentName='footer-link'
			hidePadding={true}
			size='sm'
			variant='secondary'
			label={label}/>
	</footer>;
};

const formatHeaderHeading = (heading, recordCount) => {
	return (typeof recordCount !== 'undefined' && recordCount !== null) ?
		heading + ' (' + recordCount + ')' : heading;
};

export const view = (state) => {
	const {
		useCard,
		headerHeading,
		headerRecordCount,
		headerCaption,
		headerTip,
		headerActions,
		footerLink,
		popover,
		contentData
	} = state.properties;

	LOGGING.info('snEmAiOverviewContentView', {
		force: true,
		caller: 'snEmAiOverviewContentView->view',
		extra: {headerCaption, contentData, popover}
	});

	const heading = {label: formatHeaderHeading(headerHeading, headerRecordCount), size: 'md'};
	const caption = headerCaption ? {label: headerCaption, size: 'md'} : null;

	const headerMetadata = headerTip ?
		<div slot='metadata'>{_createTooltipHead(headerTip.trigger, headerTip.content)}</div> : null;

	const popoverButton = popover ?
		<div className='ai-popover-trigger'>{_createTooltipTail(popover.trigger, popover.content)}</div> : null;

	const contentMetadata = contentData ? contentData : null;

	const footerMetadata = footerLink ? _createFooterMetadata(footerLink) : null;

	const CompType = useCard ? 'now-card' : 'div';
	return <CompType size='lg'>
		<now-card-header
			heading={heading}
			caption={caption}
			actions={headerActions}>
			{headerMetadata}
		</now-card-header>
		{popoverButton}
		{contentMetadata}
		{footerMetadata}
	</CompType>;
};