export const CORRELATION_GROUP = { //
	NONE: '0',
	PRIMARY: '1',
	SECONDARY: '2',
	BOTH: '3'
};

const GROUP = { //group reference
	AUTOMATED: '1',
	RULES_BASED: '2',
	MANUAL: '3',
	CMDB: '4',
	CI_CLASS_BASED: '7',
	TEXT: '8',
	LOG_ANALYTICS: '9',
	COMPONENT_BASED: '10'
};

