import {checkForError, CONSTANTS, LOGGING} from '@devsnc/library-opws-mapping-shared';
import {CONSTANTS as IDENTIFIED_ISSUE_CONSTANTS} from '../constants';
import {actionTypes} from '@servicenow/ui-core';
import {get as _get} from 'lodash';
import {SYS_ID} from '../../../constants/uiTypes';

import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';

const {COMPONENT_BOOTSTRAPPED} = actionTypes;
const ACTION_TYPES = {
	ALERT_IDENTIFIED_ISSUE_REQUESTED: 'ALERT_IDENTIFIED_ISSUE#REQUESTED',
	ALERT_IDENTIFIED_ISSUE_STARTED: 'ALERT_IDENTIFIED_ISSUE#STARTED',
	ALERT_IDENTIFIED_ISSUE_SUCCEEDED: 'ALERT_IDENTIFIED_ISSUE#SUCCEEDED',
	ALERT_IDENTIFIED_ISSUE_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED
};

const GQL_ALERT_ISSUE_QUERY = `
query getCMDBRecord($queryConditions: String!) {
  GlideRecord_Query {
    em_alert(queryConditions: $queryConditions) {
      _results {
        sys_id {
          value
        }
        description{
          displayValue
        }
        sn_extra_data {
          _reference {
            explanation {
              displayValue
            }
          }
        }
      }
    }
  }
}
`;


const getAlertsIdentifiedIssue = createGraphQLEffect(GQL_ALERT_ISSUE_QUERY, {
	startActionType: ACTION_TYPES.ALERT_IDENTIFIED_ISSUE_STARTED,
	successActionType: ACTION_TYPES.ALERT_IDENTIFIED_ISSUE_SUCCEEDED,
	errorActionType: ACTION_TYPES.ALERT_IDENTIFIED_ISSUE_FAILED,
	variableList: ['queryConditions']
});

const getVariables = (alertId) => {
	return {
		queryConditions: `sys_id=${alertId}`
	};
};

const transform = (payload) => {
	const result = _get(payload, 'data.GlideRecord_Query.em_alert._results[0]', null);
	const {sys_id, description, sn_extra_data: {_reference: {explanation}}} = result;

	return {
		sys_id: sys_id['value'],
		description: description['displayValue'],
		explanation: explanation ? explanation['displayValue'] : ''
	};
};

/**
 * Perform all the tasks needed to do after the remediation started to invoke
 * @param coeffects
 */
const handleAlertsIdentifiedIssueResponse = coeffects => {
	const {
		dispatch,
		updateProperties,
		action: {payload}
	} = coeffects;
	const errors = checkForError({payload, dispatch, caller: 'handleAlertsIdentifiedIssueResponse'});
	if (errors) {
		LOGGING.error('handleAlertsIdentifiedIssueResponse has failed', errors);
		return;
	}

	LOGGING.info('alertIdentifiedIssueEffect', {
		force: true,
		caller: 'handleAlertsIdentifiedIssueResponse',
		extra: {payload}
	});

	const {description, explanation} = transform(payload);
	updateProperties({
		tooltip: {
			trigger: IDENTIFIED_ISSUE_CONSTANTS.TOOLTIP_TRIGGER,
			content: explanation
		},
		contentData: description
	});
};

const init = ({properties, dispatch}) => {
	const {sysId} = properties;

	if (!sysId) {
		LOGGING.error('alertIdentifiedIssueEffect expect to get as argument sysId');
		return;
	}
	dispatch(ACTION_TYPES.ALERT_IDENTIFIED_ISSUE_REQUESTED, getVariables(sysId), {stopPropagation: true});
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: init,
	[ACTION_TYPES.ALERT_IDENTIFIED_ISSUE_REQUESTED]: getAlertsIdentifiedIssue,
	[ACTION_TYPES.ALERT_IDENTIFIED_ISSUE_SUCCEEDED]: handleAlertsIdentifiedIssueResponse
};


const actions = {
	[ACTION_TYPES.ALERT_IDENTIFIED_ISSUE_REQUESTED]: {
		schema: {
			type: 'object',
			properties: {
				alertId: SYS_ID
			},
			required: ['alertId']
		}
	}
};

export const alertIdentifiedIssueEffect = {
	ACTION_TYPES,
	getVariables,
	actionHandlers,
	actions
};
