import '@servicenow/now-card';
import '@servicenow/now-label-value';
import '@servicenow/now-avatar';
import {LOGGING} from '@devsnc/library-opws-mapping-shared';
import {t} from 'sn-translate';

const ASSIGN_TO = t('Assign to');
const OWNED_BY = t('Owned by');
const CLASS = t('Class');
const LOCATION = t('Location');
const UNDEFINED = t('Undefined');
const UNASSIGNED = t('Unassigned');

/**
 * UI component that returns CI details such as:
 * CI name, CI class, CI location, CI assignee CI and ownership.
 * this component is a part of the overview component.
 * this component uses 'now-card' component 'now-label-value-stacked' and 'now-avatar' seismic components.
 * properties:
 * sys_id, name, sys_class_name, location, owned_by, assigned_to.
 */

const ciFooterItem = (avatar, UserName, label) => {
	const imageUrl = `/${avatar}`;
	return (
		<span className='ai-ci-layout-item'>
			<now-avatar
				size='sm'
				image-src={imageUrl}
				user-name={UserName}/>

			<now-label-value-stacked
				align='vertical'
				truncated
				className='ai-truncate ai-stacked'
				delimiter=','
				items={[{label: label, value: {type: 'string', value: UserName || UNASSIGNED}}]}
				size="sm"/>
		</span>
	);
};

const ciFooter = (cmdbMetaData) => {
	const {assigned_to, assigned_to_avatar, owned_by} = cmdbMetaData || {};

	return (
		<span className='ai-ci-layout'>
			{ciFooterItem(assigned_to_avatar, assigned_to, ASSIGN_TO)}
			{ciFooterItem('', owned_by, OWNED_BY)}
		</span>
	);
};

export const snEmAiOverviewCiView = (state) => {
	const {cmdbMetaData} = state;
	const {name, sys_class_name, location} = cmdbMetaData || {};
	LOGGING.info('snEmAiOverviewCiView', {
		force: true,
		caller: 'snEmAiOverviewCiView',
		extra: {name, sys_class_name, location}
	});

	return (
		<now-card size='lg' className='full-height'>
			<now-card-header heading={{label: name}}/>
			<div className='ai-ci-layout'>
				<now-label-value-stacked
					align="vertical"
					delimiter=","
					truncated
					className='ai-truncate ai-stacked'
					items={[{label: CLASS, value: {type: 'string', value: sys_class_name || UNDEFINED}}]}
					size="md"/>
				<now-label-value-stacked
					align="vertical"
					delimiter=","
					truncated
					className='ai-truncate ai-stacked'
					items={[{label: LOCATION, value: {type: 'string', value: location || UNDEFINED}}]}
					size="md"/>
			</div>
			<now-card-divider/>
			<now-card-footer>
				{ciFooter(cmdbMetaData)}
			</now-card-footer>
		</now-card>
	);
};
