import {createCustomElement} from '@servicenow/ui-core';
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-chart-bar';
import '@servicenow/now-heading';

const LEGEND_NAME_PREFIX = 'legendName';
const generateParts = parts => {
	const result = {};
	let index = 1;
	for (let part in parts) {
		result[`${LEGEND_NAME_PREFIX}${index++}`] = parts[part].occurrences;
	}
	return result;
};

const generateLegend = parts => {
	const result = {};

	let index = 1;
	for (let part in parts) {
		result[`${LEGEND_NAME_PREFIX}${index}`] = {
			'x': 'entityName',
			'y': `${LEGEND_NAME_PREFIX}${index++}`,
			'label': `${part} (${Number(parts[part].ratio).toFixed(2)}%)`
		};
	}
	return result;
};

const view = ({properties}) => {
	const {entity: {distribution: {relativeParts: parts}}, entity: {entity: name}} = properties;

	const seriesData = [{
		...generateParts(parts),
		'entityName': ' '
	}];

	const seriesConfig = generateLegend(parts);

	return (
		<Fragment>
			<now-heading wontWrap={true} label={name} level="5" variant="title-tertiary"/>
			<now-chart-bar seriesData={seriesData}
				series-config={seriesConfig}
				orientation="horizontal" type="normalized" order-by="dataOrder" width="auto" height="auto"
				extra-margin={{'top': 0, 'bottom': -10, 'right': 10, 'left': -10}}
				tooltip-config={{'legendOnly': true}} register-position="bottom"
				bar-width={15}
				show-tooltip
				register-config={{
					justifyItems: 'start',
					disableGrid: true,
					expandToFit: true,
					legendOnly: true,
					config: {
						fontSize: '11px'
					}
				}}
				x-axis-config={{strokeWidth: 0, tickConfig: {disabled: true}}}
				y-axis-type="linear" y-format=".2f"
				hide-gridlines
				hide-y-axis
				y-grid-config={{}}
				accessibility-config={{'monochrome': true}}>
			</now-chart-bar>
		</Fragment>);
};

createCustomElement('sn-em-ai-overview-chart-bar-wrapper', {
	view,
	properties: {
		entity: {
			default: null
		}
	},
	eventHandlers: [{
		events: ['click'],
		effect({action: {payload: {event}}}) {
			event.stopPropagation();
		},
		capture: true
	}
	]
});
