import {createCustomElement} from '@servicenow/ui-core';
import {alertIdentifiedIssueEffect} from './actions/alertIdentifiedIssueEffect';
import {SYS_ID} from '../../constants/uiTypes';

/**
 * This is a provider component responsible for fetching the metrics data of the identified issues module.
 *
 * ```jsx
 * <sn-em-ai-overview-identified-issue sysId=''  />
 * ```
 *
 * @seismicElement 'sn-em-ai-overview-identified-issue'
 * @summary his is a provider component responsible for fetching the metrics data of the identified issues module.
 */
createCustomElement('sn-em-ai-overview-identified-issue', {
	properties: {
		/**
         * Specifies a specific Alert's sys_id.
         * @type {string}
         */
		sysId: {
			default: null,
			required: true,
			schema: SYS_ID
		},

		tooltip: {
			default: null,
			selectable: true
		},

		contentData: {
			default: '',
			selectable: true
		},

		// TODO where do I get this from or is it hardcoded like in PRC?
		targetTab: {
			default: 'REL:b650f757c722001049a0362c14c26090',
			selectable: true
		}
	},
	actionHandlers: {
		...alertIdentifiedIssueEffect.actionHandlers,
	},
	/* dispatches is only for the use of the JS documentation */
	dispatches: {}
});
