import {get as _get} from 'lodash';
import {CONSTANTS, checkForError, LOGGING} from '@devsnc/library-opws-mapping-shared';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {actionTypes} from '@servicenow/ui-core';
import {t} from 'sn-translate';
import {SYS_ID} from '../../../constants/uiTypes';

const GQL_MEANINGFUL_ENTITIES_QUERY = `
query getMeaningfulEntites($queryConditions: String!) {
  GlideRecord_Query {
    em_alert_extra_data(queryConditions: $queryConditions) {
      _results {
         meaningful_entities {
              value
         }
      }
    }
  }
}
`;

const {COMPONENT_BOOTSTRAPPED} = actionTypes;
const OTHERS = t('Others');

const ACTION_TYPES = {
	MEANINGFUL_ENTITIES_REQUESTED: 'MEANINGFUL_ENTITIES#REQUESTED',
	MEANINGFUL_ENTITIES_STARTED: 'MEANINGFUL_ENTITIES#STARTED',
	MEANINGFUL_ENTITIES_SUCCEEDED: 'MEANINGFUL_ENTITIES#SUCCEEDED',
	MEANINGFUL_ENTITIES_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED
};


const getMeaningfulEntities = createGraphQLEffect(GQL_MEANINGFUL_ENTITIES_QUERY, {
	startActionType: ACTION_TYPES.MEANINGFUL_ENTITIES_STARTED,
	successActionType: ACTION_TYPES.MEANINGFUL_ENTITIES_SUCCEEDED,
	errorActionType: ACTION_TYPES.MEANINGFUL_ENTITIES_FAILED,
	variableList: ['queryConditions']
});

const getVariables = (alertId) => {
	return {
		queryConditions: `alert=${alertId}`
	};
};

/**
 * Sort parts in entities from biggest to lowest
 * also if more than 4 parts exist the 5th and above will be accumulated
 * and displayed as 'Others'
 * @param entity
 * @returns {{distribution: {relativeParts: {}}}}
 */
const sortAndEmphesizePartsInEntity = entity => {
	const {distribution} = entity;
	const modifiedParts = {};
	// small verification we've got parts to begin with
	if (distribution && distribution.relativeParts) {
		const others ={
			ratio: 0,
			occurrences: 0
		}; // initialize accumulator
		const keys = Object.keys(distribution.relativeParts);
		// move key of part into its value so it will be easy to sort and extract it
		// later on
		const parts = keys.map(key => ({
			key,
			...distribution.relativeParts[key]
		}));
		// sort from biggest to lowest
		parts.sort((part1, part2) => part2.ratio - part1.ratio);
		// extract key and recreate relativeParts again
		parts.forEach((part, index) => {
			if (index < 4 || parts.length === 5) {
				modifiedParts[part.key] = part;
			} else {
				others.ratio += Number.parseFloat(part.ratio);
				others.occurrences += Number.parseFloat(part.occurrences);
			}
		});
		// if accumulator greater than zero, add 'Others' category with its value
		if (others.occurrences) modifiedParts[OTHERS] = {...others};
	}
	return {
		...entity,
		distribution: {
			relativeParts: modifiedParts
		}
	};
};
const transform = (payload) => {
	const result = _get(payload, 'data.GlideRecord_Query.em_alert_extra_data._results[0]', null);
	const {meaningful_entities} = result;


	let entities = [];
	try {
		entities = JSON.parse(meaningful_entities['value']);
		// sort parts in entities from the biggest to the lowest
		entities = entities.map(sortAndEmphesizePartsInEntity);
	} catch (Exception) {
		LOGGING.error(Exception);
	}

	return {
		entities
	};
};

/**
 * Perform all the tasks needed to do after the remediation started to invoke
 * @param coeffects
 */
const handleMeaningfulEntitiesResult = coeffects => {
	const {
		dispatch,
		updateProperties,
		action: {payload}
	} = coeffects;

	const errors = checkForError({payload, dispatch, caller: 'handleMeaningfulEntitiesResult'});
	if (errors) {
		LOGGING.error('handleMeaningfulEntitiesResult has failed', errors);
		return;
	}

	LOGGING.info('meaningfulEntitiesEffect', {
		force: true,
		caller: 'meaningfulEntitiesEffect',
		extra: {payload}
	});

	const {entities} = transform(payload);

	updateProperties({
		contentData: entities
	});
};
const init = ({properties, dispatch}) => {
	LOGGING.log('meaningfulEntitiesEffect->init');
	const {sysId} = properties;
	if (!sysId) {
		LOGGING.error('meaningfulEntitiesEffect->init expect to get as argument sysId');
		return;
	}
	dispatch(ACTION_TYPES.MEANINGFUL_ENTITIES_REQUESTED, getVariables(sysId), {stopPropagation: true});
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: init,
	[ACTION_TYPES.MEANINGFUL_ENTITIES_REQUESTED]: getMeaningfulEntities,
	[ACTION_TYPES.MEANINGFUL_ENTITIES_SUCCEEDED]: handleMeaningfulEntitiesResult
};


const actions = {
	[ACTION_TYPES.MEANINGFUL_ENTITIES_REQUESTED]: {
		schema: {
			type: 'object',
			properties: {
				alertId: SYS_ID
			},
			required: ['alertId']
		}
	}
};

/**
 * Effect that handles fetching meaningful entities mapping
 * @function meaningfulEntitiesEffect
 * @example
 *
 * // Register with component action handlers:
 * {
 *     actionHandlers: {
 *			...meaningfulEntitiesEffect.actionHandlers
 *     }
 * }
 *
 * @return {Object} meaningfulEntitiesEffect Descriptor
 */
export const meaningfulEntitiesEffect = {
	ACTION_TYPES,
	getVariables,
	actionHandlers,
	actions
};
