import {createCustomElement} from '@servicenow/ui-core';
import styles from './snEmAiOverviewListContent.scss';
import {snEmAiOverviewListContentView} from './snEmAiOvervewListContentView';

createCustomElement('sn-em-ai-overview-list-content', {
	view: snEmAiOverviewListContentView,
	styles,

	properties: {
		headerHeading: {
			default: null
		},
		rowCount: {
			default: null
		},
		maxCharLimit: {
			default: null
		},
		wordWrap: {
			default: null
		},
		table: {
			default: null
		},
		query: {
			default: null
		},
		orderByField: {
			default: null
		},
		orderByDesc: {
			default: false
		},
		columns: {
			default: null
		},
		allowUpdateHeading: {
			default: false
		}
	},

	actionHandlers: {
		'LIST_DATA_FETCH_COMPLETE': ({properties, dispatch, action}) => {
			const {allowUpdateHeading, headerHeading} = properties;

			if (allowUpdateHeading) {
				const recordCount = action.payload.count;
				const newHeaderHeading = headerHeading + ' (' + recordCount + ')';

				// update the header heading of the container according to the received records count
				dispatch('SET_PROPERTIES', {headerHeading: newHeaderHeading});
			}
		}
	}
});