import {createCustomElement} from '@servicenow/ui-core';
import {alertInsightEffect} from './actions/alertInsightEffect';
import {CONSTANTS} from './constants';
import {SYS_ID} from '../../constants/uiTypes';

const incidents = {
	total: {label: CONSTANTS.INCIDENTS.TOP_INCIDENTS, value: ''},
	current: {label: CONSTANTS.INCIDENTS.CURRENT_CI, value: ''},
	related: {label: CONSTANTS.INCIDENTS.RELATED_CI, value: ''}
};

const alerts = {
	total: {label: CONSTANTS.ALERTS.TOP_ALERTS, value: ''},
	similar: {label: CONSTANTS.ALERTS.SIMILAR_ALERTS, value: ''},
	repeated: {label: CONSTANTS.ALERTS.REPEATED_ALERTS, value: ''}
};

/**
 * This is a provider component responsible for fetching the metrics data of the Insight module.
 *
 * ```jsx
 * <sn-em-ai-overview-insight sysId=''  />
 * ```
 *
 * @seismicElement 'sn-em-ai-overview-insight'
 * @summary his is a provider component responsible for fetching the metrics data of the Insight module.
 */
createCustomElement('sn-em-ai-overview-insight', {
	initialState: {
		isCalculated: false
	},
	properties: {
		/**
         * Specifies a specific Alert's sys_id.
         * @type {string}
         */
		sysId: {
			default: null,
			required: true,
			schema: SYS_ID
		},
		/**
		 * A selectable property which holds the metrics metadata of Alert's Incidents
		 *  @type {object}
		 */
		incidents: {
			default: incidents,
			selectable: true
		},
		/**
		 * A selectable property which holds the metrics metadata of top Alerts
		 *  @type {object}
		 */
		alerts: {
			default: alerts,
			selectable: true
		}
	},
	actionHandlers: {
		...alertInsightEffect.actionHandlers,
	},
	/* dispatches is only for the use of the JS documentation */
	dispatches: {}
});
