import {createCustomElement} from '@servicenow/ui-core';
import {probableRootCauseEffect} from './actions/probableRootCauseEffect';
import {CONSTANTS} from './constants';
import {SYS_ID} from '../../constants/uiTypes';

const total = {label: CONSTANTS.PROBABLE_ROOT_CAUSE, value: ''};
const changes = {label: CONSTANTS.CHANGES, value: ''};
const alerts = {label: CONSTANTS.ALERTS, value: ''};

/**
 * This is a provider component responsible for fetching the metrics data of the Insight module.
 *
 * ```jsx
 * <sn-em-ai-overview-probable-root-cause sysId=''  />
 * ```
 *
 * @seismicElement 'sn-em-ai-overview-probable-root-cause'
 * @summary his is a provider component responsible for fetching the metrics data of the Insight module.
 */
createCustomElement('sn-em-ai-overview-probable-root-cause', {
	properties: {
		/**
         * Specifies a specific Alert's sys_id.
         * @type {string}
         */
		sysId: {
			default: null,
			required: true,
			schema: SYS_ID
		},
		/**
		 * A selectable property which holds the total counter of probable root cause
		 * @type {string} - A string representation of Number
		 */
		total: {
			default: total,
			selectable: true
		},
		/**
		 * A selectable property which holds the amount of the changes
		 * @type {string} - A string representation of Number
		 */
		changes: {
			default: changes,
			selectable: true
		},
		/**
		 * A selectable property which holds the amount of the alerts
		 * @type {string} - A string representation of Number
		 */
		alerts: {
			default: alerts,
			selectable: true
		},
		targetTab: {
			default: 'REL:b650f757c722001049a0362c14c26090',
			selectable: true
		}
	},
	actionHandlers: {
		...probableRootCauseEffect.actionHandlers,
	},
	/* dispatches is only for the use of the JS documentation */
	dispatches: {
		/**
         * Dispatched when the Probable Root cause component is loaded on the first time.
         * The payload is empty
         * @type {{value: object}}
         */
		'PROBABLE_ROOT_CAUSE#REQUESTED': {}
	}
});
