import {createCustomElement} from '@servicenow/ui-core';
import styles from './snEmAiOverviewPopoverWrapper.scss';

createCustomElement('popover-content-wrapper', {
	styles,
	view: (state) => {
		const {label, triggerId} = state.properties;

		return (
			<div
				className='popover-content-wrapper'
				aria-describedby={triggerId}
				slot='content'>
				{label}
			</div>);
	},
	properties: {
		label: {
			default: null
		},
		triggerId: {
			default: null
		}
	}
});