import '@servicenow/now-rich-text';
import '@servicenow/now-button';
import '@servicenow/now-modal';
import {t} from 'sn-translate';
import {createRef, Fragment} from '@servicenow/ui-renderer-snabbdom';
import {invokeOnNextFrame, LOGGING} from '@devsnc/library-opws-mapping-shared';

export const view = (state, {dispatch}) => {
	const {
		moreOpened,
		properties: {
			headerLabel,
			contentData = '',
			withShowMore = false
		}
	} = state;
	LOGGING.info('snEmAiOverviewIdentifiedIssueContentView', {
		force: true,
		caller: 'snEmAiOverviewIdentifiedIssueContentView',
		extra: {headerLabel, contentData, withShowMore}
	});
	const preModalStyle = 'overflow-wrap: break-word; white-space: pre-wrap; margin:0;font-size:0.95em;line-height:1.4';
	const preRef = createRef();

	const shouldShowMoreButton = function (ref) {
		const el = ref.current;

		invokeOnNextFrame(() => {
			const elemHeight = el.getBoundingClientRect().height;
			const parentHeight = el.parentElement.getBoundingClientRect().height;

			const withShowMore =  elemHeight > parentHeight;
			LOGGING.info(`should show more button? ${withShowMore}`);
			dispatch('SET_PROPERTIES', {withShowMore: withShowMore});
		}
		);
	};

	const showMoreButton = withShowMore ?
		<now-button-bare
			className='show-more'
			componentName='showMore'
			size='sm'
			variant='secondary'
			hidePadding={false}
			icon-start='chevron-down-outline'
			label={t('Show more')}
		/> : null;

	LOGGING.info(`showMoreButton exists? ${Boolean(showMoreButton)}`);

	return (
		<Fragment>
			<div className='text-container'>
				<now-rich-text
					ref={preRef}
					html={`<pre style='${preModalStyle}'>${contentData}</pre>`}
					hook-update={() => shouldShowMoreButton(preRef)}
				/>
			</div>
			{showMoreButton}
			<now-modal
				componentName='modalShowMore'
				header-label={headerLabel}
				manage-opened={true}
				footer-actions={[{'label': t('Done'), 'variant': 'primary', 'clickActionType': 'NOW_MODAL#OPENED_SET'}]}
				size='lg'
				opened={moreOpened}>
				<now-rich-text html={`<pre style='${preModalStyle}'>${contentData}</pre>`}/>
			</now-modal>
		</Fragment>);
};