import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {deepFreeze} from '@devsnc/library-opws-mapping-shared';
import {LOGGING, CONSTANTS, checkForError} from '@devsnc/library-opws-mapping-shared';

const ACTION_NAMES = deepFreeze({
	GOG_MORE_INFO: Symbol('GOG_MORE_INFO'),
	GOG_EXPAND: Symbol('GOG_EXPAND')
});

const ACTION_TYPES = deepFreeze({
	GOG_CORRELATION_TOGGLE: 'GOG_CORRELATION#TOGGLE',
	GOG_CORRELATION_INVOKED: 'GOG_CORRELATION#INVOKED',
	GOG_CORRELATION_STARTED: 'GOG_CORRELATION#STARTED',
	GOG_CORRELATION_SUCCEEDED: 'GOG_CORRELATION#SUCCEEDED',
	GOG_CORRELATION_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED
});

const GQL_QUERY = `
query getCorrelation($parentQuery: String!, $childrenQuery: String!) {
  GlideRecord_Query {
    em_alert_extra_data(queryConditions: $parentQuery) {
      _results {
        correlators {
          value
        }
      }
    }
    em_alert(queryConditions: $childrenQuery, pagination: {limit: 100}) {
      _results {
        sys_id {
          value
        }
      }
    }
  }
}`;

const getCorrelation = createGraphQLEffect(GQL_QUERY, {
	startActionType: ACTION_TYPES.GOG_CORRELATION_STARTED,
	successActionType: ACTION_TYPES.GOG_CORRELATION_SUCCEEDED,
	errorActionType: ACTION_TYPES.GOG_CORRELATION_FAILED,
	variableList: ['parentQuery', 'childrenQuery']
});

const _sort = (a, b) => {
	const x = a.name.toLowerCase();
	const y = b.name.toLowerCase();
	return (x < y) ? -1 : 1;
};


const _excerptCorrelations = payload => {
	//Get the correlation from the parent
	const {data: {GlideRecord_Query: {em_alert_extra_data: {_results}}}} = payload;
	const {correlators: {value}} = _results[0];

	let correlators = {};
	try {
		correlators = JSON.parse(value);
	} catch (exception) {
		LOGGING.error(`setCorrelation-> Error parsing correlation [${value}]`);
	}

	const correlations = [];
	for (let key in correlators) {
		correlations.push({
			name: key,
			isExpanded: false,
			count: correlators[key]
		});
	}

	correlations.sort(_sort);
	return correlations;
};

const _excerptChildrenAlerts = payload => {
	//Get all children
	const {data: {GlideRecord_Query: {em_alert: {_results}}}} = payload;
	const children = _results.map(item => item.sys_id.value);
	return children;
};

const setCorrelation = coeffects => {
	LOGGING.info('setCorrelation');
	const {
		dispatch,
		updateState,
		updateProperties,
		action: {payload}
	} = coeffects;

	const errors = checkForError({payload, dispatch, caller: 'setCorrelation'});
	if (errors) {
		return;
	}

	const correlations = _excerptCorrelations(payload);
	const children = _excerptChildrenAlerts(payload);
	updateState({
		path: 'locals.groupOfGroups',
		value: {correlations, children},
		operation: 'set'
	});
	updateProperties({nowMode: 'active'});
};

const correlationToggleExpanded = coeffects => {
	LOGGING.info('correlationToggleExpanded');
	const {state: {locals}, action: {payload}, updateState} = coeffects;
	const {groupOfGroups: {correlations}} = locals;
	const {actionType, index} = payload;
	const cor = correlations[index];

	if (actionType !== ACTION_NAMES.GOG_EXPAND || !cor) {
		LOGGING.error('notificationsEffect->correlationToggleExpanded wrong actionType or no correlation index', {actionType, index});
		return;
	}
	const {isExpanded} = cor;
	updateState({
		path: `locals.groupOfGroups.correlations[${index}].isExpanded`,
		value: !isExpanded,
		operation: 'set'
	});
};

const actions = {
	[ACTION_TYPES.GOG_CORRELATION_TOGGLE]: {private: true}
};

const actionHandlers = {
	[ACTION_TYPES.GOG_CORRELATION_TOGGLE]: correlationToggleExpanded,
	[ACTION_TYPES.GOG_CORRELATION_INVOKED]: getCorrelation,
	[ACTION_TYPES.GOG_CORRELATION_SUCCEEDED]: setCorrelation
};

export const groupOfGroupsEffect = {
	ACTION_NAMES,
	ACTION_TYPES,
	actions,
	actionHandlers
};

