import {checkForError, CONSTANTS, LOGGING} from '@devsnc/library-opws-mapping-shared';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {actionTypes} from '@servicenow/ui-core';
import {alertImpactedServicesEffect} from './alertImpactedServicesEffect';
import {get as _get} from 'lodash';
import {ALERT_TYPE, CORRELATION_GROUP} from '../constants/alertTypes';

const {COMPONENT_BOOTSTRAPPED, COMPONENT_ERROR_THROWN} = actionTypes;

const ACTION_TYPES = {
	ALERT_OVERVIEW_TAB_STARTED: 'ALERT_OVERVIEW#STARTED',
	ALERT_RECORD_STARTED: 'ALERT_RECORD#STARTED',
	ALERT_RECORD_SUCCEEDED: 'ALERT_RECORD#SUCCEEDED',
	ALERT_RECORD_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED
};

const _REF = '_reference';

const GQL_QUERY = `
query getAlertRecord($queryConditions: String!) {
  GlideRecord_Query {
    em_alert(queryConditions: $queryConditions) {
      _results {
        sys_id {
          value
        }
        cmdb_ci {
          value
        }
        sn_priority_group {
          value
        }
        correlation_group {
          value
        }
        group {
          _reference {
            source {
              value
            }
          }
        }
        sn_extra_data {
          _reference {
            category {
              value
            }
          }
        }
        group_source {
          value
        }
        parent {
          _reference {
            group {
              _reference {
                source {
                    value
                }
              }
            }
          }
        }
      }
    }
  }
}`;

const getAlertRecordMetaData = createGraphQLEffect(GQL_QUERY, {
	startActionType: ACTION_TYPES.ALERT_RECORD_STARTED,
	successActionType: ACTION_TYPES.ALERT_RECORD_SUCCEEDED,
	errorActionType: ACTION_TYPES.ALERT_RECORD_FAILED,
	variableList: ['queryConditions']
});

const isOfAlertType = (alertTypeKey, data) => {
	const {
		correlation_group,
		group: {
			source: group
		},
		sn_extra_data: {
			category
		},
		parent: {
			group: {
				source: parent_group
			}
		}
	} = data;

	const relevantGroup = [CORRELATION_GROUP.SECONDARY, CORRELATION_GROUP.BOTH].includes(correlation_group) ? parent_group : group;

	const alertType = ALERT_TYPE[alertTypeKey];
	LOGGING.debug(`Testing if alert type ${alertTypeKey}
		{correlation_group: ${correlation_group}, group: ${relevantGroup}, (${group}/${parent_group}), category: ${category}} is in 
		{correlation_group: ${alertType.correlation_group}, group: [${alertType.group}], category: [${alertType.category}]}`);

	return (
		alertType.correlation_group === correlation_group &&
		alertType.group.includes(relevantGroup) &&
		(!alertType.category.length || alertType.category.includes(category))
	);
};

const transformAlertType = (data) => {
	let found = false;
	let layout = null;

	// TODO use arrays forEach? find? or something else? what about checking all types for inconsistencies?
	for (let alertTypeKey in ALERT_TYPE) {
		found = isOfAlertType(alertTypeKey, data);
		if (found) {
			if (layout) {
				LOGGING.error(`Alert type layout was already set to ${layout} - can not set again to ${alertTypeKey}`);
			} else {
				layout = alertTypeKey;
				LOGGING.info(`Setting alert type layout to ${alertTypeKey}`);
			}
		}
	}

	//FIXME ERROR!
	if (!layout) {
		LOGGING.error(`Did not find any alert type for this alert: ${data.sys_id}`);
	}

	return {...data, layout};
};

const transform = (payload) => {
	const _copyValues = (obj) => {
		let data = {};

		for (let key in obj) {
			if (obj && obj.hasOwnProperty(key)) {
				if (obj[key] && obj[key].hasOwnProperty(_REF)) {
					data[key] = _copyValues(obj[key][_REF]);
				} else {
					data[key] = obj[key] ? obj[key].value : null;
				}
			}
		}

		return data;
	};

	const results = _get(payload, 'data.GlideRecord_Query.em_alert._results[0]', null);
	const data = _copyValues(results);

	return transformAlertType(data);
};


/**
 *
 * @param coeffects
 */
const setAlertRecordMetaData = coeffects => {
	LOGGING.info('setAlertRecordMetaData');

	const {
		dispatch,
		updateState,
		action: {payload},
		properties
	} = coeffects;

	const errors = checkForError({payload, dispatch, caller: 'setAlertRecordMetaData'});
	if (errors) {
		return;
	}

	const alertMetaData = transform(payload);

	if (Object.keys(alertMetaData).length > 0) {
		updateState({alertMetaData});
		const {sys_id: alertId} = alertMetaData;
		LOGGING.info('alertMetaDataEffect->setAlertRecordMetaData-> get alert impactedServices');
		dispatch(alertImpactedServicesEffect.ACTION_TYPES.IMPACTED_SERVICES_REQUESTED, {alertId}, {stopPropagation: true});
	} else {
		const {sysId} = properties;
		LOGGING.error('setAlertRecordMetaData-> Can not fetch the prerequisite meta data for alert-' + sysId);
	}
};


const getVariables = (alertId) => {
	return {
		queryConditions: `sys_id=${alertId}`
	};
};

const init = ({properties, dispatch}) => {
	const packageJson = require('../../../package');
	LOGGING.info(`${packageJson.name} package version : ${packageJson.version}`, {
		force: true, caller: 'init',
		extra: {packageJson}
	});

	const {sysId} = properties;
	if (!sysId) {
		LOGGING.error('sn-em-ai-overview expect to get as argument alertId');
		return;
	}
	dispatch(ACTION_TYPES.ALERT_OVERVIEW_TAB_STARTED, getVariables(sysId), {stopPropagation: true});
};


const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: init,
	[ACTION_TYPES.ALERT_OVERVIEW_TAB_STARTED]: getAlertRecordMetaData,
	[ACTION_TYPES.ALERT_RECORD_SUCCEEDED]: setAlertRecordMetaData,
	[COMPONENT_ERROR_THROWN]: ({action: {payload}}) => LOGGING.error(JSON.stringify(payload))
};

const actions = {
	[ACTION_TYPES.ALERT_OVERVIEW_TAB_STARTED]: {
		schema: {
			type: 'object',
			properties: {
				queryConditions: {type: 'string'}
			},
			required: ['queryConditions']
		}
	}
};

export const alertMetaDataEffect = {
	ACTION_TYPES,
	getVariables,
	actionHandlers,
	actions
};

