import {checkForError, CONSTANTS, LOGGING} from '@devsnc/library-opws-mapping-shared';
import {createHttpEffect} from '@servicenow/ui-effect-http';
import {actionTypes} from '@servicenow/ui-core';
import {CONSTANTS as ANOMALY_CONSTANTS} from '../constants';
import {SYS_ID} from '../../../constants/uiTypes';

const {COMPONENT_BOOTSTRAPPED} = actionTypes;
const ACTION_TYPES = {
	ALERT_ANOMALY_REQUESTED: 'ALERT_ANOMALY#REQUESTED',
	ALERT_ANOMALY_STARTED: 'ALERT_ANOMALY#STARTED',
	ALERT_ANOMALY_SUCCEEDED: 'ALERT_ANOMALY#SUCCEEDED',
	ALERT_ANOMALY_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED
};

//const getAlertsAnomaly = createHttpEffect('/api/now/em_actions/getAnomalyInfoForAlert', {
const getAlertsAnomaly = createHttpEffect('/api/sn_em_ai/ai_alert_actions/getMBAnomalyPointsForAlert', {
	method: 'GET',
	queryParams: ['alertId'],
	startActionType: ACTION_TYPES.ALERT_ANOMALY_STARTED,
	successActionType: ACTION_TYPES.ALERT_ANOMALY_SUCCEEDED,
	errorActionType: ACTION_TYPES.ALERT_ANOMALY_FAILED
});

const getVariables = (alertId) => {
	return {
		alertId
	};
};


/**
 * Perform all the tasks needed to do after the remediation started to invoke
 * @param coeffects
 */
const handleAlertsAnomalyResponse = coeffects => {
	const {
		dispatch,
		updateProperties,
		action: {payload}
	} = coeffects;
	const errors = checkForError({payload, dispatch, caller: 'handleAlertsAnomalyResponse'});
	if (errors) {
		LOGGING.error('handleAlertsAnomalyResponse has failed', errors);
		return;
	}

	LOGGING.info('alertAnomalyEffect', {
		force: true,
		caller: 'handleAlertsAnomalyResponse',
		extra: {payload}
	});

	const {
		tooltip,
		count,
		expected,
		metrics,
		timezone,
		detectionType
	} = payload.result;

	updateProperties({
		tooltip: {
			trigger: ANOMALY_CONSTANTS.TOOLTIP_TRIGGER,
			content: tooltip
		},
		count,
		expected,
		metrics,
		timezone,
		detectionType
	});
};

const init = ({properties, dispatch}) => {
	const {sysId} = properties;

	if (!sysId) {
		LOGGING.error('alertAnomalyEffect expect to get as argument sysId');
		return;
	}
	dispatch(ACTION_TYPES.ALERT_ANOMALY_REQUESTED, getVariables(sysId), {stopPropagation: true});
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: init,
	[ACTION_TYPES.ALERT_ANOMALY_REQUESTED]: getAlertsAnomaly,
	[ACTION_TYPES.ALERT_ANOMALY_SUCCEEDED]: handleAlertsAnomalyResponse
};


const actions = {
	[ACTION_TYPES.ALERT_ANOMALY_REQUESTED]: {
		schema: {
			type: 'object',
			properties: {
				alertId: SYS_ID
			},
			required: ['alertId']
		}
	}
};

export const alertAnomalyEffect = {
	ACTION_TYPES,
	getVariables,
	actionHandlers,
	actions
};
