import {actionTypes, createCustomElement} from '@servicenow/ui-core';
import {view} from './snEmAiOverviewIdentifiedIssueContentView';
import styles from './snEmAiOverviewIdentifiedIssueContent.scss';

const {COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED} = actionTypes;

/**
 * Check if the component is ready for render and has enough data to opt out from suspended
 */
const _isReady = coeffects => {
	const {properties, updateProperties} = coeffects;
	const {contentData} = properties;

	//Check if on BOOTSTRAPPED we already got what we need
	if (contentData){
		updateProperties({contentData});

	}
};

createCustomElement('sn-em-ai-overview-identified-issue-content', {
	styles,
	view,
	initialState: {
		moreOpened: false
	},
	properties: {
		headerLabel: {
			default: null
		},
		contentData: {
			default: null
		},
		withShowMore: {
			default: false
		}
	},

	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: _isReady,
		[COMPONENT_PROPERTY_CHANGED]: _isReady,
		'SET_PROPERTIES': ({action, updateProperties}) => {
			updateProperties({...action.payload});
		},

		'NOW_BUTTON_BARE#CLICKED': ({action, updateState}) => {
			action.stopPropagation();

			const {componentName} = action.meta;

			if (componentName === 'showMore') {
				updateState({moreOpened: true});
			}
		},

		'NOW_MODAL#OPENED_SET': ({action, updateState}) => {
			action.stopPropagation();

			const {componentName} = action.meta;

			if (componentName === 'modalShowMore') {
				updateState({moreOpened: false});
			}
		}
	}
});