import {actionTypes, createCustomElement} from '@servicenow/ui-core';
import {snEmAiOverviewMetricView} from './snEmAiOverviewMetricView';
import styles from './snEmAiOverviewMetric.scss';

const {COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED} = actionTypes;
const schema = {
	type: 'object',
	properties: {
		label: {type: 'string'},
		value: {type: 'string'}
	},
	required: ['label', 'value']
};

/**
 * Check if the component is ready for render and has enough data to opt out from suspended
 */
const _isReady = coeffects => {
	const {properties, action, updateProperties} = coeffects;
	const {nowMode, header} = properties;

	//Check if on BOOTSTRAPPED we all ready got what we need
	if (header){
		updateProperties({nowMode: 'active'});
		return;
	}

	//Check PROPERTY_CHANGED the contentData since it is must have a value, then it means we can render
	const {payload: {name, value}} = action;
	if (nowMode === 'suspend' && name === 'scoreTotal' && value && value.value) {
		updateProperties({nowMode: 'active'});

	}
};


/**
 * This is a generic component which presents a total number as a primary figure in  the header.
 * Beneath thereof in the footer it should show the sub calculated numbers that from them the total was reckon
 *
 * ```jsx
 * <sn-em-ai-overview-metric
 *    scoreTotal={{label:'Title', value:'23'}}
 *    scoreLeft={{label:'subTitle', value:'13'}}
 *    scoreRight={{label:'subTitle', value:'10'}} />
 * ```
 *
 * @seismicElement sn-em-ai-overview-metric
 * @summary The component displays metrics counter - The total in the header and its calculated sub-parts in the footer.
 */
createCustomElement('sn-em-ai-overview-metric', {
	view: snEmAiOverviewMetricView,
	styles,
	properties: {
		/**
		 * An object that contains the settings for the top score.
		 * @type {object}
		 */
		scoreTotal: {
			default: null,
			required: true,
			schema
		},
		/**
		 * An object that contains the settings for the left score.
		 * @type {object}
		 */
		scoreLeft: {
			default: null,
			required: true,
			schema
		},
		/**
		 * An object that contains the settings for the right score.
		 * @type {object}
		 */
		scoreRight: {
			default: null,
			required: true,
			schema
		},
		/**
		 * If true, the bottom scores will be aligned to the side
		 * @type {boolean}
		 */
		halfWidth: {
			default: false,
			required: false,
			schema: {
				type: 'boolean'
			}
		}
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: _isReady,
		[COMPONENT_PROPERTY_CHANGED]: _isReady
	},
	/* dispatches is only for the use of the JS documentation */
	dispatches: {}
})
;
