import {actionTypes} from '@servicenow/ui-core';
import {LOGGING} from '@devsnc/library-opws-mapping-shared';
import {groupOfGroupsEffect} from './groupOfGroupsEffect';
import {NOTIFICATION} from '../snEmAiOverviewNotificationView';

const {COMPONENT_BOOTSTRAPPED} = actionTypes;
const {ACTION_TYPES} = groupOfGroupsEffect;

const alertClosed = coeffects => {
	LOGGING.info('alertClosed', {caller: 'notificationsEffect'});
	const {updateProperties} = coeffects;
	updateProperties({show: false});
};

const modalToggle = coeffects => {
	const {state: {locals}, updateState} = coeffects;
	const {modalConfig: {isOpened}} = locals;
	LOGGING.info('modalToggle', {caller: 'notificationsEffect', extra: {isOpened}});
	updateState({
		path: 'locals.modalConfig.isOpened',
		value: !isOpened,
		operation: 'set'
	});
};

const buttonClicked = coeffects => {
	LOGGING.info('buttonClicked', {caller: 'notificationsEffect'});
	const {dispatch, action: {payload}} = coeffects;
	const {actionType} = payload;
	if (actionType !== groupOfGroupsEffect.ACTION_NAMES.GOG_EXPAND) {
		LOGGING.error(`notificationsEffect->buttonClicked wrong actionType ${actionType}`);
		return;
	}
	dispatch(groupOfGroupsEffect.ACTION_TYPES.GOG_CORRELATION_TOGGLE, payload);
};

const init = coeffects => {
	LOGGING.info('init', {caller: 'notificationsEffect'});
	const {state, properties, dispatch, updateProperties} = coeffects;
	const {properties: {type}} = state;
	const {alertMetaData: {sys_id}} = properties;

	if (!sys_id) {
		LOGGING.error('notificationsEffect expect to get as argument alertId');
		return;
	}

	switch (type) {
	case NOTIFICATION.PRIORITY:
		updateProperties({nowMode: 'active'});
		break;
	case NOTIFICATION.GROUP_OF_GROUPS:
		dispatch(ACTION_TYPES.GOG_CORRELATION_INVOKED,
			{parentQuery: `alert=${sys_id}`, childrenQuery: `parent=${sys_id}`});
		break;
	default:
		LOGGING.error(`notificationsEffect no such render type ${type}`);
		break;
	}
};


const actions = {
	...groupOfGroupsEffect.actions,
	'NOW_ALERT#ACTION_CLICKED': {private: true},
	'NOW_TEXT_LINK#CLICKED': {private: true},
	'NOW_MODAL#OPENED_SET': {private: true},
	'NOW_MODAL#FOOTER_ACTION_CLICKED': {private: true},
	'NOW_BUTTON_ICONIC#CLICKED': {private: true}
};

const actionHandlers = {
	...groupOfGroupsEffect.actionHandlers,
	[COMPONENT_BOOTSTRAPPED]: init,
	'NOW_ALERT#ACTION_CLICKED': alertClosed, // Dismissed icon
	'NOW_TEXT_LINK#CLICKED': modalToggle, // Alert embedded message link
	'NOW_MODAL#OPENED_SET': modalToggle, //Esc || Dismissed icon
	'NOW_MODAL#FOOTER_ACTION_CLICKED': modalToggle, //Close Button
	'NOW_BUTTON_ICONIC#CLICKED': buttonClicked //Expanded
};

export const notificationsEffect = {
	actions,
	actionHandlers,
};

