import {CONSTANTS, checkForError, LOGGING} from '@devsnc/library-opws-mapping-shared';
import {createHttpEffect} from '@servicenow/ui-effect-http';
import {actionTypes} from '@servicenow/ui-core';
import {SYS_ID} from '../../../constants/uiTypes';

const {COMPONENT_BOOTSTRAPPED} = actionTypes;
import {CONSTANTS as PRC_CONSTANTS} from '../constants';

const ACTION_TYPES = {
	PROBABLE_ROOT_CAUSE_REQUESTED: 'PROBABLE_ROOT_CAUSE#REQUESTED',
	PROBABLE_ROOT_CAUSE_STARTED: 'PROBABLE_ROOT_CAUSE#STARTED',
	PROBABLE_ROOT_CAUSE_SUCCEEDED: 'PROBABLE_ROOT_CAUSE#SUCCEEDED',
	PROBABLE_ROOT_CAUSE_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED
};

const getProbableRootCause = createHttpEffect('/api/now/em_actions/getPossibleRootCausesForAlert', {
	method: 'GET',
	queryParams: ['alertId'],
	startActionType: ACTION_TYPES.PROBABLE_ROOT_CAUSE_STARTED,
	successActionType: ACTION_TYPES.PROBABLE_ROOT_CAUSE_SUCCEEDED,
	errorActionType: ACTION_TYPES.PROBABLE_ROOT_CAUSE_FAILED
});

const getVariables = (alertId) => {
	return {
		alertId
	};
};

/**
 * Perform all the tasks needed to do after the remediation started to invoke
 * @param coeffects
 */
const handleProbableRootCauseResult = coeffects => {
	const {
		dispatch,
		updateProperties,
		action: {payload}
	} = coeffects;
	const errors = checkForError({payload, dispatch, caller: 'handleProbableRootCauseResult'});
	if (errors) {
		LOGGING.error('handleProbableRootCauseResult has failed', errors);
		return;
	}


	LOGGING.info('probableRootCauseEffect', {
		force: true,
		caller: 'handleProbableRootCauseResult',
		extra: {payload}
	});

	const {result: {total, changes, alerts}} = payload;
	updateProperties({
		total: {
			label: PRC_CONSTANTS.PROBABLE_ROOT_CAUSE,
			value: String(total)
		},
		changes: {
			label: PRC_CONSTANTS.CHANGES,
			value: String(changes)
		},
		alerts: {
			label: PRC_CONSTANTS.ALERTS,
			value: String(alerts)
		}
	});
};

const init = ({properties, dispatch}) => {
	const {sysId} = properties;
	if (!sysId) {
		LOGGING.error('probableRootCauseEffect->init expect to get as argument sysId');
		return;
	}
	dispatch(ACTION_TYPES.PROBABLE_ROOT_CAUSE_REQUESTED, getVariables(sysId), {stopPropagation: true});
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: init,
	[ACTION_TYPES.PROBABLE_ROOT_CAUSE_REQUESTED]: getProbableRootCause,
	[ACTION_TYPES.PROBABLE_ROOT_CAUSE_SUCCEEDED]: handleProbableRootCauseResult
};


const actions = {
	[ACTION_TYPES.PROBABLE_ROOT_CAUSE_REQUESTED]: {
		schema: {
			type: 'object',
			properties: {
				alertId: SYS_ID
			},
			required: ['alertId']
		}
	}
};

/**
 * Effect that handles fetching the counters metrics for PRCA
 *
 * @function probableRootCauseEffect
 * @example
 * import {snEmAiOverviewMetric} from '@sn-em-ai-overview-probable-root-cause/probableRootCauseEffect';
 *
 * // Register with component action handlers:
 * {
 *     actionHandlers: {
 *			...probableRootCauseEffect.actionHandlers
 *     }
 * }
 *
 * @return {Object} probableRootCauseEffect Descriptor
 */
export const probableRootCauseEffect = {
	ACTION_TYPES,
	getVariables,
	actionHandlers,
	actions
};
