import {createCustomElement, actionTypes} from '@servicenow/ui-core';
import {LOGGING} from '@devsnc/library-opws-mapping-shared';
import {view} from './snEmAiOverviewContentView';
import styles from './snEmAiOverviewContent.scss';
import {SYS_ID} from '../../constants/uiTypes';

const {COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED} = actionTypes;

/**
 * Check if the component is ready for render and has enough data to opt out from suspended
 */
const _isReady = coeffects => {
	const {properties, action, updateProperties} = coeffects;
	const {nowMode, popover, headerTip} = properties;

	//Check if on BOOTSTRAPPED we all ready got what we need
	if (popover || headerTip){
		updateProperties({nowMode: 'active'});
		return;
	}

	//Check PROPERTY_CHANGED the popover || header-tip since it is must have a value, then it means we can render
	const {payload: {name, value}} = action;
	if (nowMode === 'suspend') {
		if ((name === 'popover' || name === 'headerTip') && value) {
			updateProperties({nowMode: 'active'});
		}
	}
};

createCustomElement('sn-em-ai-overview-content', {
	view,
	styles,
	initialState: {},

	properties: {
		/**
         * Specifies the CI sys_id associated to the alert if exists.
         * @type {string}
         */
		sysId: {
			default: undefined,
			schema: SYS_ID
		},
		// FIXME There is a problem adding a now-record-list-connected inside a card, this will use
		//       a div instead of a now-card (for now I hope)
		useCard: {
			default: true
		},

		headerHeading: {
			default: null
		},

		headerCaption: {
			default: null
		},

		headerRecordCount: {
			default: null
		},

		headerTip: {
			default: null
		},

		headerActions: {
			default: null
		},

		popover: {
			default: null
		},

		footerLink: {
			default: null
		},

		contentData: {
			default: null
		},
		/**
         * Specifies which Workspace L2 tab to open when clicked on the component.
         * @type {object}
         */
		targetTab: {
			default: undefined,
			required: false,
			schema: {type: 'string'}
		}
	},

	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: _isReady,
		[COMPONENT_PROPERTY_CHANGED]: _isReady,
		'NOW_BUTTON_BARE#CLICKED': ({properties, action, dispatch}) => {
			action.stopPropagation();

			const {meta: {componentName}} = action;

			if (componentName === 'footer-link') {
				const {targetTab} = properties;
				if (targetTab)
					dispatch('NOW_RECORD_FORM_BLOB#TAB_SELECTED', {selectedTabName: targetTab});
				else
					LOGGING.warn('Can not switch tab to ' + targetTab);
			}
		},
		'SET_PROPERTIES': ({action, updateProperties}) => {
			action.stopPropagation();

			// update properties according to contained component (from the action's payload)
			updateProperties({...action.payload});
		}
	}
});