import {t} from 'sn-translate';

const PRIORITY = {
	1: t('Urgent'),
	2: t('High'),
	3: t('Moderate'),
	4: t('Low')
};

export const priority = state => {
	const {properties: {alertMetaData, impactedServices}} = state;
	const {sn_priority_group} = alertMetaData;
	const impactedServicesCounter = (impactedServices) ? impactedServices.length : null;
	if (sn_priority_group > 2 || !impactedServicesCounter)
		return null;

	const service = t('service');
	const services = t('services');
	const content = t('Priority is {0} since alert is impacting {1} {2}.',
		PRIORITY[sn_priority_group],
		impactedServicesCounter,
		impactedServicesCounter === 1 ? service : services);
	const label = t('Learn more about priority calculation');

	const items = {
		id: 'priority',
		status: 'info',
		icon: 'circle-info-outline',
		content,
		textLinkProps: {
			label,
			href: 'https://docs.servicenow.com/bundle/orlando-it-operations-management/page/product/event-management/concept/alert-priority.html#alert-priority'
		},
		action: {type: 'dismiss'}
	};

	return (<span><now-alert config-aria-live="polite" {...items}/></span>);
};
