import {LOGGING, deepFreeze} from '@devsnc/library-opws-mapping-shared';
import {groupOfGroupsEffect} from '../actions/groupOfGroupsEffect';
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-modal';
import '@servicenow/now-collapse';
import '@servicenow/now-button';
import '@servicenow/now-record-list-connected';
import {t} from 'sn-translate';

const {ACTION_NAMES} = groupOfGroupsEffect;
const I18N = deepFreeze({
	MODAL_HEADER: t('Correlations'),
	MODAL_SUB_HEADER: t('Expand a correlation to view its related alerts'),
	MODAL_BUTTON_CLOSE: t('Close'),
	CORRELATIONS_EXPAND: t('Expand'),
	CORRELATIONS_COLLAPSE: t('Collapse'),
	ALERT_MESSAGE: t(' on how these alerts were correlated'),
	ALERT_LINK_LABEL: t('More info'),
});

const getCorrelationJsx = (children, correlation, index) => {
	const {name, isExpanded, count} = correlation;
	const icon = isExpanded ? 'chevron-down-fill' : 'chevron-right-fill';
	const tooltip = isExpanded ? I18N.CORRELATIONS_COLLAPSE : I18N.CORRELATIONS_EXPAND;
	const correlationName = `${name} (${count})`;
	const query = `alertIN${children.join()}^correlatorsLIKE,${name},^ORcorrelatorsLIKE"${name},`;

	return (
		<span>
			<div className='correlations-category'>
				<now-button-iconic
					icon={icon}
					tooltip-content={tooltip}
					configAria={{'aria-label': tooltip}}
					append-to-payload={{actionType: ACTION_NAMES.GOG_EXPAND, index}}
					size='md'
					variant='tertiary'
					bare/>
				<span className='correlations-category-header'>{correlationName}</span>
			</div>
			<now-collapse className='correlations-collapse' expanded={isExpanded}>
				<now-record-list-connected-snapshot
					table={'em_alert_extra_data'}
					key={`list_${correlationName}`}
					columns={'alert, application_service, component'}
					query={query}
					limit='4'
					hide-header
					hide-title-row-count
					hide-refresh-button
					hide-last-refreshed-text
					hide-links
					hide-row-selector
					hide-declarative-actions
					hide-view-all
					hide-column-sorting
					hide-cell-filter
					hide-column-filtering
				/>
			</now-collapse>
		</span>
	);
};

const getAlertMessageJsx = () => {
	return (
		<span>
			<now-text-link append-to-payload={{actionType: ACTION_NAMES.GOG_MORE_INFO}} label={I18N.ALERT_LINK_LABEL} href="javascript:void(0)"/>
			{I18N.ALERT_MESSAGE}
		</span>
	);
};

export const groupOfGroups = state => {
	LOGGING.info('groupOfGroups');
	const {locals: {modalConfig, groupOfGroups}} = state;
	const {correlations, children} = groupOfGroups;

	if (!correlations) {
		LOGGING.error('groupOfGroups expect to have correlations & children', {
			caller: 'groupOfGroups',
			extra: {groupOfGroups}
		});
		return null;
	}

	const correlationList = correlations.map(getCorrelationJsx.bind(null, children));
	const alertConf = {
		id: 'groupOfGroups',
		icon: 'circle-info-outline',
		status: 'info',
		content: {'type': 'jsx', 'value': getAlertMessageJsx()},
		action: {type: 'dismiss'}
	};

	return (
		<Fragment>
			<now-alert config-aria-live="polite" {...alertConf}/>
			<now-modal
				header-label={I18N.MODAL_HEADER}
				footer-actions={[{'label': I18N.MODAL_BUTTON_CLOSE, 'variant': 'primary'}]}
				size="lg"
				opened={modalConfig.isOpened}>
				<span className='correlations-sub-header'>{I18N.MODAL_SUB_HEADER}</span>
				{correlationList}
			</now-modal>
		</Fragment>
	);
};
