import {CONSTANTS, checkForError, LOGGING} from '@devsnc/library-opws-mapping-shared';
import {createHttpEffect} from '@servicenow/ui-effect-http';
import {actionTypes} from '@servicenow/ui-core';
import {SYS_ID} from '../../../constants/uiTypes';

const {COMPONENT_BOOTSTRAPPED} = actionTypes;
import {CONSTANTS as ALERTS_IN_GROUP_CONSTANTS} from '../constants';

const ACTION_TYPES = {
	ALERTS_IN_GROUP_REQUESTED: 'ALERTS_IN_GROUP#REQUESTED',
	ALERTS_IN_GROUP_STARTED: 'ALERTS_IN_GROUP#STARTED',
	ALERTS_IN_GROUP_SUCCEEDED: 'ALERTS_IN_GROUP#SUCCEEDED',
	ALERTS_IN_GROUP_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED
};


const getAlertsInGroup = createHttpEffect('/api/now/em_actions/getAlertsInGroup', {
	method: 'GET',
	queryParams: ['alertId'],
	startActionType: ACTION_TYPES.ALERTS_IN_GROUP_STARTED,
	successActionType: ACTION_TYPES.ALERTS_IN_GROUP_SUCCEEDED,
	errorActionType: ACTION_TYPES.ALERTS_IN_GROUP_FAILED
});

const getVariables = (alertId) => {
	return {
		alertId
	};
};

/**
 * Perform all the tasks needed to do after the remediation started to invoke
 * @param coeffects
 */
const handleAlertsInGroupResult = coeffects => {
	const {
		dispatch,
		updateProperties,
		action: {payload},
		properties: {sysId}
	} = coeffects;

	const errors = checkForError({payload, dispatch, caller: 'handleAlertsInGroupResult'});
	if (errors) {
		LOGGING.error('handleAlertsInGroupResult has failed', errors);
		return;
	}

	LOGGING.info('alertsInGroupEffect', {
		force: true,
		caller: 'alertsInGroupEffect',
		extra: {payload}
	});

	const {
		result: {
			alerts,
			metrics: {membersCount, reasoning},
			cisInGroup
		}
	} = payload;
	updateProperties({
		total: {
			label: ALERTS_IN_GROUP_CONSTANTS.ALERTS_IN_GROUP,
			value: String(membersCount)
		},
		reasoning: {
			label: ALERTS_IN_GROUP_CONSTANTS.GROUP_REASONING,
			value: String(reasoning)
		},
		alertsQuery: `sys_idIN${alerts.join()}`,
		totalCIs: cisInGroup.length,
		query: `sys_idIN${cisInGroup.join()}`
	});
};
const init = ({properties, dispatch}) => {
	LOGGING.log('alertsInGroupEffect->init');
	const {sysId} = properties;
	if (!sysId) {
		LOGGING.error('alertsInGroupEffect->init expect to get as argument sysId');
		return;
	}
	dispatch(ACTION_TYPES.ALERTS_IN_GROUP_REQUESTED, getVariables(sysId), {stopPropagation: true});
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: init,
	[ACTION_TYPES.ALERTS_IN_GROUP_REQUESTED]: getAlertsInGroup,
	[ACTION_TYPES.ALERTS_IN_GROUP_SUCCEEDED]: handleAlertsInGroupResult
};


const actions = {
	[ACTION_TYPES.ALERTS_IN_GROUP_REQUESTED]: {
		schema: {
			type: 'object',
			properties: {
				alertId: SYS_ID
			},
			required: ['alertId']
		}
	}
};

/**
 * Effect that handles fetching All CIs in group and reasoning
 * @function alertsInGroupEffect
 * @example
 * import {alertsInGroupEffect} from '@sn-em-ai-overview-alerts-in-group/alertsInGroupEffect';
 *
 * // Register with component action handlers:
 * {
 *     actionHandlers: {
 *			...alertsInGroupEffect.actionHandlers
 *     }
 * }
 *
 * @return {Object} alertsInGroupEffect Descriptor
 */
export const alertsInGroupEffect = {
	ACTION_TYPES,
	getVariables,
	actionHandlers,
	actions
};
