import {createCustomElement} from '@servicenow/ui-core';
import {snEmAiOverviewNotificationView} from './snEmAiOverviewNotificationView';
import {notificationsEffect} from './actions/notificationsEffect';
import styles from './snEmAiOverviewNotification.scss';

/**
 * This component displays an alert notification that explains why the Alert's priority is high or urgent.
 *
 * ```jsx
 * <sn-em-ai-overview-notification show priority='1' impactedServices=[] />
 * ```
 *
 * @seismicElement 'sn-em-ai-overview-notification'
 * @summary display an alert notification that explains why the Alert's priority is high or urgent.
 */
createCustomElement('sn-em-ai-overview-notification', {
	view: snEmAiOverviewNotificationView,
	styles,
	initialState: {
		locals: {
			modalConfig: {
				isOpened: false,
			},
			groupOfGroups: {
				correlations: undefined,
				children: undefined
			},
			priority: {}
		}
	},
	properties: {
		/**
		 * Determine whether to show the notification according the logic or at all not displaying it
		 * @type {boolean}
		 */
		show: {
			default: true
		},
		/**
		 * Specifies the type(Symbol) of the notification to render
		 * @type {string}
		 */
		type: {
			default: null
		},
		/**
		 * Specifies the alert meta data
		 * @type {object}
		 */
		alertMetaData: {
			default: null
		},
		/**
		 * The impacted services array for a specific alert.
		 * We are using this array for fetching the number of impact services for a specific alert
		 * @type {Array}
		 */
		impactedServices: {
			default: null
		}
	},
	actions: {
		...notificationsEffect.actions
	},
	actionHandlers: {
		...notificationsEffect.actionHandlers
	}
});
