import {deepFreeze, LOGGING} from '@devsnc/library-opws-mapping-shared';
import {t} from 'sn-translate';
import '@servicenow/now-badge';
import '@servicenow/now-heading';
import '@servicenow/now-record-list-connected';

/* COMPONENTS */
import './components/sn-em-ai-overview-notification';
import './components/sn-em-ai-overview-ci';
import './components/sn-em-ai-overview-metric-graph';
import './components/sn-em-ai-overview-metric';
import './components/sn-em-ai-overview-last-activities';
import './components/sn-em-ai-overview-content';
import './components/sn-em-ai-overview-identified-issue-content';
import './components/sn-em-ai-overview-list-content';
import './components/sn-em-ai-overview-anomaly-content';
import './components/sn-em-ai-overview-meaningful-log-properties';

/* PROVIDERS */
import './providers/sn-em-ai-overview-alerts-in-group';
import './providers/sn-em-ai-overview-probable-root-cause';
import './providers/sn-em-ai-overview-insight';
import './providers/sn-em-ai-overview-anomaly';
import './providers/sn-em-ai-overview-identified-issue';
import './providers/sn-em-ai-overview-meaningful-entities';

const _activeProviders = {};
const _activeComponents = {};

const _getUniqueId = (type, name, sysId) => {
	return `${type}_${name}_${sysId}`; //Unique among workspace TABS
};

const _PROVIDER_TYPES = deepFreeze({
	rootCause: {
		name: 'rootCause',
		type: 'sn-em-ai-overview-probable-root-cause'
	},
	alertsInGroup: {
		name: 'alertsInGroup',
		type: 'sn-em-ai-overview-alerts-in-group'
	},
	insight: {
		name: 'insight',
		type: 'sn-em-ai-overview-insight'
	},
	anomaly: {
		name: 'anomaly',
		type: 'sn-em-ai-overview-anomaly'
	},
	identifiedIssue: {
		name: 'identifiedIssue',
		type: 'sn-em-ai-overview-identified-issue'
	},
	meaningfulEntities: {
		name: 'meaningfulEntities',
		type: 'sn-em-ai-overview-meaningful-entities'
	}
});

/**
 *
 * @param sysId
 * @param provider
 * @returns {string}
 * @private
 */
const _createProvider = (sysId, provider) => {
	const id = _getUniqueId('provider', provider.name, sysId);
	const ProviderType = provider.type; //JSX standards requires 'custom' element to be Capital Case ;-)
	_activeProviders[sysId][id] = <ProviderType sys-id={sysId} component-id={id}/>;
	return id;
};

const _PROVIDERS = {
	rootCause: sysId => _createProvider(sysId, _PROVIDER_TYPES.rootCause),
	alertsInGroup: sysId => _createProvider(sysId, _PROVIDER_TYPES.alertsInGroup),
	insight: sysId => _createProvider(sysId, _PROVIDER_TYPES.insight),
	anomaly: sysId => _createProvider(sysId, _PROVIDER_TYPES.anomaly),
	identifiedIssue: sysId => _createProvider(sysId, _PROVIDER_TYPES.identifiedIssue),
	meaningfulEntities: sysId => _createProvider(sysId, _PROVIDER_TYPES.meaningfulEntities)
};

const _COMPONENTS = {
	notification: (notificationType, alertMetaData, impactedServices) => {
		const {sys_id} = alertMetaData;
		const key = _getUniqueId('component', 'notification', sys_id);
		return _activeComponents[sys_id][key] = <sn-em-ai-overview-notification
			now-mode='suspend'
			key={key}
			type={notificationType}
			impacted-services={impactedServices}
			alert-meta-data={alertMetaData}
			className='ai-notifications -full-width'/>;
	},
	rootCause: (sysId) => {
		const key = _getUniqueId('component', 'rootCause', sysId);
		const providerId = _PROVIDERS.rootCause(sysId);

		const HEADER_HEADING = t('Probable root causes');
		const FOOTER_LINK = t('More details');
		const TARGET_TAB = 'REL:b650f757c722001049a0362c14c26090';

		const content = <sn-em-ai-overview-metric
			now-mode='suspend'
			key={key}
			half-width={true}
			score-total={`@${providerId}/total`}
			score-left={`@${providerId}/changes`}
			score-right={`@${providerId}/alerts`}/>;

		return _activeComponents[sysId][key] =
			<sn-em-ai-overview-content
				sys-id={sysId}
				key={key}
				className='ai-card-view ai-prc -full-width'
				header-heading={HEADER_HEADING}
				footer-link={FOOTER_LINK}
				content-data={content}
				target-tab={TARGET_TAB}
			/>;
	},
	alertListInGroup: (sysId) => {
		const key = _getUniqueId('component', 'alertListInGroup', sysId);
		const providerId = _PROVIDERS.alertsInGroup(sysId);
		const HEADER_HEADING = t('Alerts in group');
		const HEADER_CAPTION = t('By priority');
		const FOOTER_LINK = t('View more');
		const TARGET_TAB = 'REL:d2d526835b77001031112e8fb681c708';
		const content =
			<sn-em-ai-overview-list-content
				header-heading={HEADER_HEADING}
				row-count={4}
				max-char-limit={400}
				word-wrap={true}
				table={'em_alert'}
				query={`@${providerId}/alertsQuery`}
				order-by-field={'sn_priority'}
				order-by-desc={true}
				columns={'number, description, sn_extra_data.anomaly'}
			/>;

		return _activeComponents[sysId][key] =
			<sn-em-ai-overview-content
				sys-id={sysId}
				use-card={false}
				key={key}
				className='ai-list-view ai-alerts-in-group -full-width'
				header-heading={HEADER_HEADING}
				header-record-count={`@${providerId}/total.value`}
				header-caption={HEADER_CAPTION}
				footer-link={FOOTER_LINK}
				content-data={content}
				target-tab={TARGET_TAB}
			/>;
	},
	topIncidents: (sysId) => {
		const key = _getUniqueId('component', 'topIncidents', sysId);
		const providerId = _PROVIDERS.insight(sysId);

		const HEADER_HEADING = t('Top incidents');
		const FOOTER_LINK = t('More details');
		const TARGET_TAB = 'REL:f56109e79362030054fc31f6357ffb2a';

		const content = <sn-em-ai-overview-metric
			now-mode='suspend'
			key={key}
			score-total={`@${providerId}/incidents.total`}
			score-left={`@${providerId}/incidents.current`}
			score-right={`@${providerId}/incidents.related`}/>;

		return _activeComponents[sysId][key] =
			<sn-em-ai-overview-content
				sys-id={sysId}
				key={key}
				className='ai-card-view ai-top-incidents'
				header-heading={HEADER_HEADING}
				footer-link={FOOTER_LINK}
				content-data={content}
				target-tab={TARGET_TAB}
			/>;
	},
	topAlerts: (sysId) => {
		const key = _getUniqueId('component', 'topAlerts', sysId);
		const providerId = _PROVIDERS.insight(sysId);
		const HEADER_HEADING = t('Top alerts');
		const FOOTER_LINK = t('More details');
		const TARGET_TAB ='REL:51158e669326030054fc31f6357ffb10'; //`@${providerId}/alerts.targetTab`

		const content = <sn-em-ai-overview-metric
			now-mode='suspend'
			key={key}
			score-total={`@${providerId}/alerts.total`}
			score-left={`@${providerId}/alerts.similar`}
			score-right={`@${providerId}/alerts.repeated`}/>;

		return _activeComponents[sysId][key] =
			<sn-em-ai-overview-content
				sys-id={sysId}
				key={key}
				className='ai-card-view ai-top-alerts'
				header-heading={HEADER_HEADING}
				footer-link={FOOTER_LINK}
				content-data={content}
				target-tab={TARGET_TAB}
			/>;
	},
	ci: (sysId, cmdbCi) => {
		const ciSysId = cmdbCi ? cmdbCi : undefined;
		const key = _getUniqueId('component', 'ci', sysId);
		return _activeComponents[sysId][key] = <sn-em-ai-overview-ci
			now-mode='suspend'
			key={key}
			sys-id={ciSysId}
			className='ai-card-view ai-overview-ci'/>;
	},
	impactedServices: (sysId, impactedServices) => {
		LOGGING.info('snEmAiOverviewFactory -> impactedServices', {
			caller: 'snEmAiOverviewFactory',
			extra: {impactedServices}
		});

		if (!impactedServices)
			return null;

		const HEADER_HEADING = t('Impacted services');
		const HEADER_CAPTION = t('By business criticality');
		const FOOTER_LINK = t('View more');
		const TARGET_TAB = 'REL:f58a765f53601300cdbdddeeff7b1217';
		const key = _getUniqueId('component', 'impactedServices', sysId);

		const content =
			<sn-em-ai-overview-list-content
				header-heading={HEADER_HEADING}
				row-count={5}
				table={'cmdb_ci_service_auto'}
				query={`sys_idIN${impactedServices.join()}`}
				order-by-field={'busines_criticality'}
				columns={'name, severity, busines_criticality, owned_by'}
			/>;
		return _activeComponents[sysId][key] =
			<sn-em-ai-overview-content
				sys-id={sysId}
				use-card={false}
				key={key}
				className='ai-list-view ai-impacted-services -full-width'
				header-heading={HEADER_HEADING}
				header-record-count={impactedServices.length}
				header-caption={HEADER_CAPTION}
				footer-link={FOOTER_LINK}
				content-data={content}
				target-tab={TARGET_TAB}
			/>;
	},
	cisInGroup: (sysId) => {
		const key = _getUniqueId('component', 'cisInGroup', sysId);
		const providerId = _PROVIDERS.alertsInGroup(sysId);

		const HEADER_HEADING = t('Configuration items');
		const HEADER_CAPTION = t('By name');
		const FOOTER_LINK = t('View more');
		const TARGET_TAB = 'REL:f0a20f7107231010a8d29c871fd300e0';

		const content =
			<sn-em-ai-overview-list-content
				header-heading={HEADER_HEADING}
				row-count={5}
				table={'cmdb_ci'}
				query={`@${providerId}/query`}
				order-by-field={'name'}
				order-by-desc={false}
				columns={'name, sys_class_name, location, owned_by, assignment_group'}
			/>;
		return _activeComponents[sysId][key] =
			<sn-em-ai-overview-content
				sys-id={sysId}
				use-card={false}
				key={key}
				className='ai-list-view ai-cis-in-group -full-width'
				header-heading={HEADER_HEADING}
				header-record-count={`@${providerId}/totalCIs`}
				header-caption={HEADER_CAPTION}
				content-data={content}
				footer-link={FOOTER_LINK}
				target-tab={TARGET_TAB}
			/>;
	},
	identifiedIssue: (sysId) => {
		const key = _getUniqueId('component', 'identifiedIssue', sysId);
		const providerId = _PROVIDERS.identifiedIssue(sysId);
		const HEADER_HEADING = t('Identified issue');
		const FOOTER_LINK = t('View Surrounding logs');
		const TARGET_TAB = 'cf1198e907261010e7cd9c871fd300f7';

		const content = <sn-em-ai-overview-identified-issue-content
			header-label={HEADER_HEADING}
			content-data={`@${providerId}/contentData`}
		/>;

		return _activeComponents[sysId][key] = <sn-em-ai-overview-content
			now-mode='suspend'
			sys-id={sysId}
			key={key}
			className='ai-card-view ai-identified-issue -full-width'
			header-heading={HEADER_HEADING}
			footer-link={FOOTER_LINK}
			popover={`@${providerId}/tooltip`}
			content-data={content}
			target-tab={TARGET_TAB}
		/>;
	},
	anomaly: (sysId) => {
		const key = _getUniqueId('component', 'anomaly', sysId);
		const providerId = _PROVIDERS.anomaly(sysId);
		const HEADER_HEADING = t('Anomaly');

		const content = <sn-em-ai-overview-anomaly-content
			metrics={`@${providerId}/metrics`}
			count={`@${providerId}/count`}
			timezone={`@${providerId}/timezone`}
			detectionType={`@${providerId}/detectionType`}
			expected={`@${providerId}/expected`}
		/>;

		return _activeComponents[sysId][key] = <sn-em-ai-overview-content
			className='ai-card-view ai-anomaly -full-width'
			sys-id={sysId}
			key={key}
			header-heading={HEADER_HEADING}
			popover={`@${providerId}/tooltip`}
			content-data={content}/>;
	},
	meaningfulEntities: (sysId) => {
		const key = _getUniqueId('component', 'meaningfulEntities', sysId);
		const providerId = _PROVIDERS.meaningfulEntities(sysId);
		const HEADER_LABEL = t('Meaningful log properties');

		const content = <sn-em-ai-overview-meaningful-log-properties
			now-mode='suspend'
			content-data={`@${providerId}/contentData`}
		/>;

		return _activeComponents[sysId][key] = <sn-em-ai-overview-content
			now-mode='suspend'
			className='ai-card-view ai-meaningful-entity -full-width'
			sys-id={sysId}
			key={key}
			header-heading={HEADER_LABEL}
			header-tip={`@${providerId}/tooltip`}
			content-data={content}
		/>;
	}
};

export const componentsFactory = Object.freeze({
	getComponents: sysId => {
		//Singleton check if empty
		if (!_activeComponents[sysId]) {
			_activeComponents[sysId] = {};
			_activeProviders[sysId] = {};
		}
		return _COMPONENTS;
	},
	getActiveComponents: sysId => _activeComponents[sysId],
	getActiveProviders: sysId => _activeProviders[sysId]
}
);
