import {createCustomElement} from '@servicenow/ui-core';
import {CONSTANTS} from './constants';
import {alertsInGroupEffect} from './actions/alertsInGroup';
import {SYS_ID} from '../../constants/uiTypes';

const total = {label: CONSTANTS.ALERTS_IN_GROUP, value: '' || '0'};
const reasoning = {label: CONSTANTS.GROUP_REASONING, value: ''};


/**
 * This is a provider component responsible for fetching the aggregate metrics data for an Alert of type 'Group'.
 *
 * ```jsx
 * <sn-em-ai-overview-alerts-in-group  sysId=''  />
 * ```
 *
 * @seismicElement 'sn-em-ai-overview-alerts-in-group'
 * @summary fetching the aggregate metrics data for an Alert of type 'Group'.
 */
createCustomElement('sn-em-ai-overview-alerts-in-group', {
	properties: {
		/**
         * Specifies a specific Alert's sys_id.
         * @type {string}
         */
		sysId: {
			default: null,
			required: true,
			schema: SYS_ID
		},
		total: {
			default: total,
			selectable: true
		},
		/**
         * A selectable property which holds the reasoning categories of the alerts within the group
         * @type {string} - A string representation of Number
         */
		reasoning: {
			default: reasoning,
			selectable: true
		},
		totalCIs: {
			default: 0,
			selectable: true
		},
		/**
         * A selectable property which contains the array of alerts with in a group
         * @type {array} - An array of alerts with in a group
         */
		alerts: {
			default: undefined,
			selectable: true,
			schema: {type: 'array'}
		},
		/**
		 * A selectable property which contains the query of all related alerts
		 * @type {string} - A related alerts query
		 */
		alertsQuery: {
			default: undefined,
			selectable: true,
			schema: {type: 'string'}
		},

		/**
         * A selectable property which contains the query for the alerts with in a group DB view
         * @type {string} - encodedQuery for em_alerts_in_group DB View
         */
		query: {
			default: undefined,
			selectable: true,
			schema: {type: 'string'}
		}
	},

	actionHandlers: {
		...alertsInGroupEffect.actionHandlers,
	},
	/* dispatches is only for the use of the JS documentation */
	dispatches: {
		/**
         * Dispatched when the Probable Root cause component is loaded on the first time.
         * The payload is empty
         * @type {{value: object}}
         */
		'ALERTS_IN_GROUP#REQUESTED': {}
	}
});
