import {CONSTANTS, checkForError, LOGGING} from '@devsnc/library-opws-mapping-shared';
import {createHttpEffect} from '@servicenow/ui-effect-http';
import {SYS_ID} from '../constants/uiTypes';

const ACTION_TYPES = {
	IMPACTED_SERVICES_REQUESTED: 'IMPACTED_SERVICES#REQUESTED',
	IMPACTED_SERVICES_STARTED: 'IMPACTED_SERVICES#STARTED',
	IMPACTED_SERVICES_SUCCEEDED: 'IMPACTED_SERVICES#SUCCEEDED',
	IMPACTED_SERVICES_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED
};

const getImpactedServices = createHttpEffect('/api/now/em_actions/getImpactedServiceByAlert', {
	method: 'GET',
	queryParams: ['alertId'],
	startActionType: ACTION_TYPES.IMPACTED_SERVICES_STARTED,
	successActionType: ACTION_TYPES.IMPACTED_SERVICES_SUCCEEDED,
	errorActionType: ACTION_TYPES.IMPACTED_SERVICES_FAILED
});



/**
 * Perform all the tasks needed to do after the remediation started to invoke
 * @param coeffects
 */
const handleImpactedServicesResult = coeffects => {
	LOGGING.log('alertImpactedServicesEffect->handleImpactedServicesResult');
	const {
		dispatch,
		updateState,
		action: {payload}
	} = coeffects;

	const errors = checkForError({payload, dispatch, caller: 'handleImpactedServicesResult'});
	if (errors) {
		LOGGING.error('handleImpactedServicesResult failed', errors);
		return;
	}

	LOGGING.info('handleImpactedServicesResult', {
		force: true,
		caller: 'handleImpactedServicesResult',
		extra: {payload}
	});

	const {result: {impactedServices}} = payload;
	updateState({impactedServices});
};

const actionHandlers = {
	[ACTION_TYPES.IMPACTED_SERVICES_REQUESTED]: getImpactedServices,
	[ACTION_TYPES.IMPACTED_SERVICES_SUCCEEDED]: handleImpactedServicesResult
};


const actions = {
	[ACTION_TYPES.IMPACTED_SERVICES_REQUESTED]: {
		schema: {
			type: 'object',
			properties: {
				alertId: SYS_ID
			},
			required: ['alertId']
		}
	}
};

export const alertImpactedServicesEffect = {
	ACTION_TYPES,
	actionHandlers,
	actions
};
