import {actionTypes, createCustomElement} from '@servicenow/ui-core';
import {snEmAiOverview} from './snEmAiOverviewView';
import styles from './snEmAiOverview.scss';
import {alertMetaDataEffect} from './actions/alertMetaDataEffect';
import {alertImpactedServicesEffect} from './actions/alertImpactedServicesEffect';
import {SYS_ID} from './constants/uiTypes';
import {LOGGING} from '@devsnc/library-opws-mapping-shared';

const {COMPONENT_BOOTSTRAPPED} = actionTypes;

// TODO consider moving to its own 'action' for getPackageVersion()
const init = () => {
	const packageJson = require('../../package');
	LOGGING.updateOptions({prefix: 'OVERVIEW'});
	LOGGING.info(`${packageJson.name} package version : ${packageJson.version}`, {
		force: true, caller: 'init',
		extra: {packageJson}
	});
}

/**
 * This component contains the content of the overview tab within the Agent Workspace Alert intelligent page
 *
 * ```jsx
 * <sn-em-ai-overview sys-id=''></sn-em-ai-overview>
 * ```
 *
 * @seismicElement sn-em-ai-overview
 * @summary The component displays a grid of metrics per a given alert.
 */
createCustomElement('sn-em-ai-overview', {
	view: snEmAiOverview,
	styles,
	initialState: {
		alertMetaData: null,
		impactedServices: null
	},
	properties: {
		/**
         * Specifies a specific Alert's sys_id.
         * @type {string}
         */
		sysId: {
			default: null,
			required: true,
			schema: SYS_ID
		}
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: init,
		...alertMetaDataEffect.actionHandlers,
		...alertImpactedServicesEffect.actionHandlers
	},
	actions: {
		...alertMetaDataEffect.actions,
		...alertImpactedServicesEffect.actions
	},
	/* dispatches is only for the use of the JS documentation */
	dispatches: {
		/**
         * Dispatched when the overview tab is loaded on the first time.
         * The payload is empty
         * @type {{value: object}}
         */
		[alertMetaDataEffect.ACTION_TYPES.ALERT_OVERVIEW_TAB_STARTED]: {}
	}
});

