import {LOGGING} from '@devsnc/library-opws-mapping-shared';
import '@servicenow/now-score';
import {Fragment} from '@servicenow/ui-renderer-snabbdom';

const generateScoreJsx = (score, size = 'xs') => {
	if (!score || !score.label)
		return null;

	return <now-score-basic
		hide-whitespace={true}
		heading={score.label}
		score={score.value}
		heading-position='top'
		score-size={size} />;
};

export const snEmAiOverviewMetricView = (state) => {
	const {
		scoreTotal,
		scoreLeft,
		scoreRight,
		halfWidth = false
	} = state.properties;

	LOGGING.info('snEmAiOverviewMetricView', {
		force: true,
		caller: 'snEmAiOverviewMetricView',
		extra: {scoreTotal, scoreLeft, scoreRight}
	});

	const topScoreElm = generateScoreJsx(scoreTotal, 'md');

	const scoreLeftElm = generateScoreJsx(scoreLeft);
	const scoreRightElm = generateScoreJsx(scoreRight);

	const classStyles = 'ai-metric-footer' + (halfWidth ? ' -half-width': '');
	return <Fragment>
		{topScoreElm}
		<span className={classStyles}>
			{scoreLeftElm}
			{scoreRightElm}
		</span>
	</Fragment>;
};