import {CONSTANTS, checkForError, LOGGING} from '@devsnc/library-opws-mapping-shared';
import {createHttpEffect} from '@servicenow/ui-effect-http';
import {actionTypes} from '@servicenow/ui-core';
import {insightConfiguration} from './insightConfiguration';
import {SYS_ID} from '../../../constants/uiTypes';

const {COMPONENT_BOOTSTRAPPED} = actionTypes;
const ACTION_TYPES = {
	ALERT_INSIGHT_REQUESTED: 'ALERT_INSIGHT#REQUESTED',
	ALERT_INSIGHT_STARTED: 'ALERT_INSIGHT#STARTED',
	ALERT_INSIGHT_SUCCEEDED: 'ALERT_INSIGHT#SUCCEEDED',
	ALERT_INSIGHT_RELATED_TASKS_REQUESTED: 'ALERT_INSIGHT_RELATED_TASKS#REQUESTED',
	ALERT_INSIGHT_RELATED_TASKS_STARTED: 'ALERT_INSIGHT_RELATED_TASKS#STARTED',
	ALERT_INSIGHT_RELATED_TASKS_SUCCEEDED: 'ALERT_INSIGHT_RELATED_TASKS#SUCCEEDED',
	ALERT_INSIGHT_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED
};

const calculateRelatedTask = createHttpEffect('/api/now/em_alert_insight/getRelatedTaskResults', {
	method: 'POST',
	dataParam: ['data'],
	startActionType: ACTION_TYPES.ALERT_INSIGHT_RELATED_TASKS_STARTED,
	successActionType: ACTION_TYPES.ALERT_INSIGHT_RELATED_TASKS_SUCCEEDED,
	errorActionType: ACTION_TYPES.ALERT_INSIGHT_FAILED
});

const getAlertsInsight = createHttpEffect('/api/now/em_alert_insight/getInsight', {
	method: 'POST',
	dataParam: ['data'],
	startActionType: ACTION_TYPES.ALERT_INSIGHT_STARTED,
	successActionType: ACTION_TYPES.ALERT_INSIGHT_SUCCEEDED,
	errorActionType: ACTION_TYPES.ALERT_INSIGHT_FAILED
});

const getVariables = (alertSysId) => {
	//Currently the requirement is to get only incidents
	const payload = {
		...insightConfiguration,
		alertSysId
	};
	return {
		data: JSON.stringify(payload)
	};
};


/**
 * @param coeffects
 */
const setRelatedTask = coeffects => {
	LOGGING.log('setRelatedTask');
	const {
		dispatch,
		properties: {sysId},
		updateState,
		action: {payload}
	} = coeffects;
	
	const errors = checkForError({payload, dispatch, caller: 'setRelatedTask'});
	if (errors) {
		LOGGING.error('alertInsightEffect->setRelatedTask has failed', errors);
		return;
	}

	//Update the current result in order to refrain from circular calculation-> retrieval
	updateState({isCalculated: true});
	dispatch(ACTION_TYPES.ALERT_INSIGHT_REQUESTED, getVariables(sysId), {stopPropagation: true});
};

/**
 *
 * @param incidents
 * @param alerts
 * @param payload
 * @returns {{alerts: *, incidents: *}}
 */
const createNewContext = (incidents, alerts, payload) => {
	const {result: {cards, tasks}} = payload;
	const relatedIncidents = tasks.filter((t) => t.typeName === 'relatedIncidents')[0].count;
	const currentIncidents = cards.filter((t) => t.typeName === 'CiIncidents')[0].count;
	const totalIncidents = String(Number(relatedIncidents) + Number(currentIncidents));
	const similarAlerts = cards.filter((t) => t.typeName === 'similarAlerts')[0].count;
	const repeatedAlerts = cards.filter((t) => t.typeName === 'repeatedAlerts')[0].count;
	const totalAlerts = String(Number(similarAlerts) + Number(repeatedAlerts));

	const newIncidentsContext = {
		...incidents,
		total: {
			...incidents.total,
			value: totalIncidents
		},
		current: {
			...incidents.current,
			value: currentIncidents
		},
		related: {
			...incidents.related,
			value: relatedIncidents
		}
	};

	const newAlertsContext = {
		...alerts,
		total: {
			...alerts.total,
			value: totalAlerts
		},
		similar: {
			...alerts.similar,
			value: similarAlerts
		},
		repeated: {
			...alerts.repeated,
			value: repeatedAlerts
		}
	};

	return {incidents: newIncidentsContext, alerts: newAlertsContext};
};


/**
 * Perform all the tasks needed to do after the remediation started to invoke
 * @param coeffects
 */
const setAlertsInsight = coeffects => {
	LOGGING.log('setAlertsInsight');
	const {
		dispatch,
		state: {isCalculated},
		properties: {incidents, alerts, sysId},
		updateProperties,
		action: {payload}
	} = coeffects;
	const errors = checkForError({payload, dispatch, caller: 'handleAlertRemediationResult'});
	if (errors) {
		LOGGING.error('alertInsightEffect->setAlertsInsight has failed', errors);
		return;
	}

	LOGGING.info('alertInsightEffect', {
		force: true,
		caller: 'alertInsightEffect',
		extra: {payload}
	});

	const newContext = createNewContext(incidents, alerts, payload);

	if (newContext.incidents.related.value === '-1' && !isCalculated) {
		//That means we need to do re -calculation
		const payload = {
			data: JSON.stringify({
				alertSysId: sysId,
				data: [{'task': 'incident'}
				]
			})
		};
		LOGGING.log('alertInsightEffect->setAlertsInsight->we need to do re-calculation or Related Tasks ');
		dispatch(ACTION_TYPES.ALERT_INSIGHT_RELATED_TASKS_REQUESTED, payload, {stopPropagation: true});
		return;
	}
	updateProperties(newContext);
};

const init = ({properties, dispatch}) => {
	const {sysId} = properties;
	if (!sysId) {
		LOGGING.error('alertInsightEffect expect to get as argument sysId');
		return;
	}
	dispatch(ACTION_TYPES.ALERT_INSIGHT_REQUESTED, getVariables(sysId), {stopPropagation: true});
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: init,
	[ACTION_TYPES.ALERT_INSIGHT_REQUESTED]: getAlertsInsight,
	[ACTION_TYPES.ALERT_INSIGHT_SUCCEEDED]: setAlertsInsight,
	[ACTION_TYPES.ALERT_INSIGHT_RELATED_TASKS_REQUESTED]: calculateRelatedTask,
	[ACTION_TYPES.ALERT_INSIGHT_RELATED_TASKS_SUCCEEDED]: setRelatedTask
};


const actions = {
	[ACTION_TYPES.ALERT_INSIGHT_REQUESTED]: {
		schema: {
			type: 'object',
			properties: {
				alertId: SYS_ID
			},
			required: ['alertId']
		}
	}
};

/**
 * Effect that handles both remediation / launch-application actions and dispatches actions accordingly:
 *  - remediation: invoke the flow designer sub flow with the specific job
 *  - launch-application: Open a new window with the specific custom URL
 *
 * @function sn-em-ai-overview-insight
 * @example
 * import {sn-em-ai-overview-insight} from 'sn-em-ai-overview-insight';
 *
 * // Register with component action handlers:
 * {
 *     actionHandlers: {
 *			...snEmAiOverviewMetric.actionHandlers
 *     }
 * }
 *
 * @return {Object} snEmAiOverviewMetric Descriptor
 */
export const alertInsightEffect = {
	ACTION_TYPES,
	getVariables,
	actionHandlers,
	actions
};
