import {createCustomElement} from '@servicenow/ui-core';
import {alertAnomalyEffect} from './actions/alertAnomalyEffect';
import {SYS_ID} from '../../constants/uiTypes';
import {CONSTANTS} from './constants';

/**
 * This is a provider component responsible for fetching the metrics data of the Anomaly module.
 *
 * ```jsx
 * <sn-em-ai-overview-anomaly sysId=''  />
 * ```
 *
 * @seismicElement 'sn-em-ai-overview-anomaly'
 * @summary his is a provider component responsible for fetching the metrics data of the Anomaly module.
 */
createCustomElement('sn-em-ai-overview-anomaly', {
	properties: {
		/**
         * Specifies a specific Alert's sys_id.
         * @type {string}
         */
		sysId: {
			default: null,
			required: true,
			schema: SYS_ID
		},

		tooltip: {
			default: null,
			selectable: true
		},

		metrics: {
			default: null,
			selectable: true,
			schema: {
				type: 'array'
			}
		},

		count: {
			default: null,
			selectable: true,
			schema: CONSTANTS.TYPE_VALUE
		},

		expected: {
			default: null,
			selectable: true,
			schema: CONSTANTS.TYPE_VALUE
		},

        timezone: {
            default: null,
            selectable: true,
            schema: {
                type: 'string'
            }
		},

		detectionType: {
            default: undefined,
            selectable: true,
            schema: {
                type: 'string',
				enum: ['ANOMALY_BASELINE_REFERENCE_INCREASE', 'ANOMALY_BASELINE_REFERENCE_DECREASE',
					'ANOMALY_STEEP_INCREASE', 'ANOMALY_STEEP_DECREASE', 'ANOMALY_ABOVE_AVERAGE', 'ANOMALY_BELOW_AVERAGE',
					'TIMELESS_TREND_INCREASE', 'TIMELESS_TREND_DECREASE', 'SIGNAL_DEAD', 'SIGNAL_ALIVE', 'NEW_SIGNAL',
					'CUSTOM'
				]
            }
        }
		
	},
	actionHandlers: {
		...alertAnomalyEffect.actionHandlers
	},
	/* dispatches is only for the use of the JS documentation */
	dispatches: {}
});
